import { IDimension, IEvent, IOptRect, IOptRectStyle, IRect } from "../types";
import { rectKeys } from "./helpers";

export const devent = (event: IEvent, options: {
  redraw?: boolean;
  passive?: boolean;
  preventDefault?: boolean;
  stopPropagation?: boolean;
} = { redraw: false, passive: false, preventDefault: true, stopPropagation: false }): IEvent => {
  if (!options.redraw) {
    event.redraw = false;
  }
  if (!options.passive) {
    event.passive = false;
  }
  if (options.preventDefault) {
    event.preventDefault();
  }
  if (options.stopPropagation) {
    event.stopPropagation();
  }
  return event;
};

export const getContainerElementByClassName = (dom: Element, className: string): Element | null => {
  let container: HTMLElement | null = dom.parentElement;
  while (container !== null && container.className.indexOf(className) === -1) {
    container = container.parentElement;
  }
  return container;
};

export const getRectStyle = (rect: IOptRect): IOptRectStyle => {
  return rectKeys.reduce((obj: IOptRectStyle, key: string) => {
    if (rect[key] === undefined) {
      return obj;
    }
    obj[key] = rect[key] + "px";
    return obj;
  }, {});
};

export const getInnerRect = (dom: Element): IRect => {
  return {
    top: parseInt(getComputedStyle(dom).paddingTop || "0", 10) + parseInt(getComputedStyle(dom).marginTop || "0", 10),
    left: parseInt(getComputedStyle(dom).paddingLeft || "0", 10) + parseInt(getComputedStyle(dom).marginLeft || "0", 10),
    width: parseInt(getComputedStyle(dom).width || "0", 10),
    height: parseInt(getComputedStyle(dom).height || "0", 10),
  };
};

export const getOuterRect = (dom: Element): IRect => {
  const rect: ClientRect = dom.getBoundingClientRect();
  return {
    top: rect.top,
    left: rect.left,
    width: rect.width,
    height: rect.height,
  };
};

export const isInViewport = (rect: IRect): boolean => {
  const html: HTMLElement = document.documentElement;
  return (
    rect.top >= 0 &&
    rect.left >= 0 &&
    rect.top + rect.height <= (window.innerHeight || html.clientHeight) &&
    rect.left + rect.width <= (window.innerWidth || html.clientWidth)
  );
};

export const getComputedStyleNumber = (dom: Element, property: string): number => {
  const style: string | null = getComputedStyle(dom)[property];
  return style ? parseFloat(style) : 0;
};

export const getTransitionDuration = (dom: Element): number => {
  return (
    getComputedStyleNumber(dom, "transitionDelay") +
    getComputedStyleNumber(dom, "transitionDuration")
  ) * 1000;
};

export const setStyle = (dom: HTMLElement, style: object): void => Object.keys(style).forEach((k: string) => dom.style[k] = String(style[k]));

export const getScaleFromFirstContainer = (dom: Element, containerClassName: string, original: IDimension): number => {
  if (!dom) {
    return 1;
  }

  let container: HTMLElement | null = dom.parentElement;
  let item: HTMLElement | null = dom as HTMLElement;
  while (container !== null && container.className.indexOf(containerClassName) === -1) {
    item = container;
    container = container.parentElement;
  }
  if (!container) {
    return 1;
  }

  const containerRect: IRect = container.getBoundingClientRect();
  const horizontal: boolean = containerRect.width > containerRect.height;

  return horizontal ? (item.offsetHeight / original.height) : (item.offsetWidth / original.width);
};

export const getClientXY = (event: IEvent): { x: number; y: number; } => {
  if (event.clientX) {
    return { x: event.clientX, y: event.clientY };
  }
  if (event.touches && event.touches[0]) {
    return { x: event.touches[0].clientX, y: event.touches[0].clientY };
  }
  if (event.changedTouches && event.changedTouches[0]) {
    return { x: event.changedTouches[0].clientX, y: event.changedTouches[0].clientY };
  }
  return { x: 0, y: 0 };
};

export const getOffsetXY = (event: IEvent): { x: number; y: number; } => {
  const rect = event.target.getBoundingClientRect();
  let { x, y } = getClientXY(event);
  x = x - rect.left;
  y = y - rect.top;
  return { x, y };
};



