import * as m from "mithril";
import { IEvent, IRect } from "../base/types";
import { IHolder } from "../base/types/product";
import { devent, getRectStyle } from "../base/utils/dom";
import { cleanString, exec } from "../base/utils/object";
import HolderContent from "./HolderContent";

export interface IHolderAttrs {
  holder: IHolder;
  className?: string;
  thumb?: boolean;
  position?: IRect;
  onclick?: (event: IEvent) => void;
}

export default class Holder implements m.ClassComponent<IHolderAttrs> {
  public view(v: m.CVnode<IHolderAttrs>) {
    const h = v.attrs.holder;
    return <div
      className={cleanString("holder", v.attrs.className)}
      style={getHolderStyle(h, v.attrs.position)}
      onclick={(event) => exec(v.attrs.onclick, devent(event))}>
      <HolderContent holder={v.attrs.holder} thumb={v.attrs.thumb} />
    </div>;
  }
}

export const getHolderStyle = (h: IHolder, position: IRect = h.position) => {
  const style: any = {
    ...getRectStyle(position),
    position: "absolute",
    overflow: "hidden",
    whiteSpace: "pre",
    display: "flex",
    alignItems: "center",
    backgroundColor: (h.content && h.content.backgroundColor) || "",
    opacity: (h.content && h.content.opacity) || "",
    imageRendering: (h.content && h.content.pixelated) ? "pixelated" : "",
  };
  return style;
};



