import EventAggregator from "eventaggregator";
import * as m from "mithril";
import { IDimension, IEvent, IOptDimensionConstrain, IOptRect, IPoint, IRect } from "../../../types";
import { devent, getClientXY, getOuterRect, getRectStyle, setStyle } from "../../../utils/dom";
import { getConstrainedRect, rectIsEqual } from "../../../utils/helpers";
import logger from "../../../utils/logger";
import { cleanString, exec } from "../../../utils/object";
import DragArea, { getAreas } from "../Area";
import "./style.scss";

const domEA = EventAggregator.getInstance("dom");

const mouseEventDelay: number = 200;

export interface IItemAttrs {
  type: string;
  onitemmovestart?: (event: IEvent, item: DragItem, source: DragArea | undefined, target: DragArea | undefined) => void;
  onitemmove?: (event: IEvent, item: DragItem, source: DragArea | undefined, target: DragArea | undefined) => void;
  onitemmoveend?: (event: IEvent, item: DragItem, source: DragArea | undefined, target: DragArea | undefined) => void;
  onitemresizestart?: (event: IEvent, item: DragItem, source?: DragArea) => void;
  onitemresize?: (event: IEvent, item: DragItem, source?: DragArea) => void;
  onitemresizeend?: (event: IEvent, item: DragItem, source?: DragArea) => void;
  onclick?: (event: IEvent, item: DragItem) => void;
  dragHandle?: m.Vnode<any, any>;
  preventMove?: boolean;
  preventResize?: boolean;
  preventPosition?: boolean;
  position?: IRect;
  constrain?: IOptDimensionConstrain;
  style?: object;
}

export default class DragItem implements m.ClassComponent<IItemAttrs> {
  private placeholderPosition?: IDimension;
  private staticPosition: IOptRect = {};
  private lastTargetArea: DragArea | undefined;
  private initialPosition: IRect = {} as IRect;
  private position: IRect = {} as IRect;
  private constrain: IOptDimensionConstrain = { minWidth: 20, minHeight: 20 };
  private placeholder: HTMLDivElement;
  private resizeSide?: string;
  private previousFixedPosition: IRect;
  private dragOffset: IPoint;
  private dom: HTMLElement;
  private dragStarted: boolean = false;
  private resizeStarted: boolean = false;
  private sourceArea?: DragArea;
  private targetArea?: DragArea;
  private events: any[] = [];
  private pressStamp: number;
  private preventDrop: boolean;
  private preventPosition: boolean;
  private data: any = {};
  private type: string = "main";
  private onitemmovestart: (event: IEvent, item: DragItem, sourceArea?: DragArea, targetArea?: DragArea) => void;
  private onitemmove: (event: IEvent, item: DragItem, sourceArea?: DragArea, targetArea?: DragArea) => void;
  private onitemmoveend: (event: IEvent, item: DragItem, sourceArea?: DragArea, targetArea?: DragArea) => void;
  private onitemresizestart: (event: IEvent, item: DragItem, sourceArea?: DragArea) => void;
  private onitemresize: (event: IEvent, item: DragItem, sourceArea?: DragArea) => void;
  private onitemresizeend: (event: IEvent, item: DragItem, sourceArea?: DragArea) => void;
  private onclick: (event: IEvent, item: DragItem) => void;

  public getBoundingClientRect(): IRect {
    return getOuterRect(this.dom);
  }

  public getData(key?: string): any {
    if (key) {
      return this.data[key];
    }
    return this.data;
  }

  public updatePosition(position: IOptRect): void {
    this.setPosition(position);
    if (this.dragStarted || this.resizeStarted) {
      this.setDOMFixedPosition();
    } else {
      this.setDOMRelativePosition();
    }
  }

  public setStaticPosition(position: IOptRect): void {
    this.staticPosition = position;
    if (this.dragStarted || this.resizeStarted) {
      this.setDOMFixedPosition({ ...this.position, ...this.staticPosition } as IOptRect);
    }
  }

  public getPosition({ snapToGrid }: { snapToGrid?: boolean } = {}): IRect {
    const position = {
      width: this.position.width,
      height: this.position.height,
      top: this.position.top,
      left: this.position.left,
    } as IRect;
    if (snapToGrid && this.targetArea) {
      const { grid }: { grid?: IDimension } = this.targetArea.getConstraints();
      if (grid) {
        return DragItem.snapToRelativeGrid(position, grid);
      }
    }
    return position;
  }

  public static withinArea(position: IRect, area: IRect, margin?: number): IPoint {
    if (margin) {
      if (position.left < area.left - margin) {
        return position;
      }
      if (position.top < area.top - margin) {
        return position;
      }

      if (position.left + position.width > area.left + area.width + margin) {
        return position;
      }
      if (position.top + position.height > area.top + area.height + margin) {
        return position;
      }
    }

    if (position.left < area.left) {
      position.left = area.left;
    }

    if (position.top < area.top) {
      position.top = area.top;
    }

    if (position.left + position.width > area.left + area.width) {
      position.left = area.left + area.width - position.width;
    }

    if (position.top + position.height > area.top + area.height) {
      position.top = area.top + area.height - position.height;
    }
    return position;
  }

  public static snapToRelativeGrid(position: IRect, grid: IDimension): IRect {
    return {
      left: (Math.round(position.left / grid.width) * grid.width),
      top: (Math.round(position.top / grid.height) * grid.height),
      width: (Math.round(position.width / grid.width) * grid.width),
      height: (Math.round(position.height / grid.height) * grid.height),
    };
  }

  public static snapToFixedGrid(position: IRect, areaRect: IRect, grid: IDimension, withOffsets: boolean = false): IRect {
    const offsets: IPoint = withOffsets ? {
      left: areaRect.left % grid.width,
      top: areaRect.top % grid.height,
    } : { left: 0, top: 0 };
    return {
      left: (Math.round(position.left / grid.width) * grid.width) + offsets.left,
      top: (Math.round(position.top / grid.height) * grid.height) + offsets.top,
      width: (Math.round(position.width / grid.width) * grid.width),
      height: (Math.round(position.height / grid.height) * grid.height),
    };
  }

  public reset(hard?: boolean) {
    if (hard) {
      this.previousFixedPosition = {} as IRect;
      this.lastTargetArea = undefined;
      this.position = {} as IRect;
      this.sourceArea = undefined;
      this.clearDOMPosition();
    }
    this.staticPosition = {};
    this.resizeSide = undefined;
    this.dragStarted = false;
    this.resizeStarted = false;
    this.targetArea = undefined;
    this.pressStamp = 0;
    this.dom.classList.remove("within-area", "dragging", "resizing");
    getAreas(this.type).forEach((area: DragArea) => {
      area.setFocus(false);
      area.setDisabled(false);
    });
    if (this.placeholder) {
      setStyle(this.placeholder, { display: "none" });
    }
  }

  public oninit(vnode: m.Vnode<IItemAttrs, DragItem>) {
    this.setState(vnode.attrs);
  }

  public oncreate(vnode: m.VnodeDOM<IItemAttrs, DragItem>) {
    this.dom = vnode.dom as HTMLElement;

    if (this.preventPosition === true) {
      this.clearDOMPosition();
    } else {
      this.setDOMRelativePosition(this.position);
    }

    this.events = [
      domEA.on(["mousemove", "touchmove"], (event: IEvent) => this.onMouseMove(event)),
      domEA.on("contextmenu", (event: IEvent) => {
        if (event.path.indexOf(this.dom) !== -1) {
          this.reset(true);
          m.redraw();
        }
      }),
      domEA.on(["mouseup", "touchend"], (event: IEvent) => this.onMouseUp(event)),
    ];
  }

  public onbeforeupdate(vnode: m.Vnode<IItemAttrs, DragItem>, old: m.VnodeDOM<IItemAttrs, DragItem>) {
    this.setState(vnode.attrs);
    return DragItem.attrsChanged(vnode.attrs, old.attrs);
  }

  public onupdate(vnode: m.VnodeDOM<IItemAttrs, DragItem>) {
    this.dom = vnode.dom as HTMLElement;
    if (this.preventPosition === true) {
      this.clearDOMPosition();
    } else {
      this.setDOMRelativePosition(this.position);
    }
  }

  public onremove(vnode: m.Vnode<IItemAttrs, DragItem>) {
    this.events.forEach((event) => event.off());
  }

  public view(vnode: m.Vnode<any, DragItem>) {
    return [
      <div className={cleanString("component--drag", "drag-item", vnode.attrs.className, !vnode.attrs.dragHandle && "handle")} style={vnode.attrs.style}
        onmousedown={(event: IEvent) => {
          this.pressStamp = Date.now();
          if (!vnode.attrs.dragHandle && !vnode.attrs.preventMove) {
            this.onDrag(devent(event));
          }
        }}
        ontouchstart={(event: IEvent) => {
          this.pressStamp = Date.now();
          if (!vnode.attrs.dragHandle && !vnode.attrs.preventMove) {
            this.onDrag(devent(event));
          }
        }}
        onmouseup={(event: IEvent) => {
          this.onClick(devent(event));
        }}
        ontouchend={(event: IEvent) => {
          this.onClick(devent(event));
        }}
      >
        {vnode.attrs.dragHandle && !vnode.attrs.preventMove && this.getDragHandleView(vnode)}
        {!vnode.attrs.preventResize && this.getResizeBorderView(vnode)}
        {vnode.children}
      </div>,
      vnode.attrs.keepPlaceholder && this.getPlaceholderView(vnode),
    ];
  }

  private getDragHandleView(vnode: m.Vnode<any, DragItem>) {
    return <div className="handle"
      onmousedown={(event: IEvent) => {
        this.onDrag(devent(event));
      }}
    >{vnode.attrs.dragHandle === true ? null : vnode.attrs.dragHandle}</div>;
  }

  private getPlaceholderView(vnode: m.Vnode<any, DragItem>) {
    return <div oncreate={(node) => {
      this.placeholder = node.dom;
      // const { width, height } = getRectStyle(this.position);
      setStyle(this.placeholder, { position: "relative", display: "none", backgroundColor: "#aaa" });
    }} className="component--drag placeholder" />;
  }

  private getResizeBorderView(vnode: m.Vnode<any, DragItem>) {
    return [
      <div onmousedown={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("left");
      }} ontouchstart={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("left");
      }} className="border left" />,
      <div onmousedown={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("right");
      }} ontouchstart={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("right");
      }} className="border right" />,
      <div onmousedown={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("top");
      }}ontouchstart={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("top");
      }}  className="border top" />,
      <div onmousedown={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("bottom");
      }} ontouchstartF={(event) => {
        devent(event, { stopPropagation: true });
        this.onResize("bottom");
      }}className="border bottom" />,
    ];
  }

  private static attrsChanged(next: IItemAttrs, prev: IItemAttrs): boolean {
    return (
      next.onitemmovestart !== prev.onitemmovestart ||
      next.onitemmove !== prev.onitemmove ||
      next.onitemmoveend !== prev.onitemmoveend ||
      next.onitemresizestart !== prev.onitemresizestart ||
      next.onitemresize !== prev.onitemresize ||
      next.onitemresizeend !== prev.onitemresizeend ||
      next.onclick !== prev.onclick ||
      next.dragHandle !== prev.dragHandle ||
      next.preventMove !== prev.preventMove ||
      next.preventResize !== prev.preventResize ||
      next.position !== prev.position ||
      next.preventPosition !== prev.preventPosition);
  }

  private setState(attrs: IItemAttrs): void {
    Object.keys(attrs).forEach((key: string) => this[key] = attrs[key]);
    this.initialPosition = attrs.position || {} as IRect;
  }

  private setInitialPosition(): IOptRect {
    const { width, height } = this.getBoundingClientRect();
    this.position = { ...this.position, top: 0, left: 0, width, height };
    return this.position;
  }

  private getSourceArea(): DragArea | undefined {
    const itemRect: IRect = this.getBoundingClientRect();
    return getAreas(this.type).find((area: DragArea) => area.withinArea(itemRect));
  }

  private getTargetArea(): DragArea | undefined {
    let targetArea: DragArea | undefined = undefined;
    getAreas(this.type).forEach((area: DragArea) => {
      area.setDisabled(false);
      area.setFocus(false);
      if (targetArea === undefined && area.withinArea(this.getBoundingClientRect())) {
        targetArea = area;
      }
    });
    return targetArea;
  }

  private getCursorOffset(x: number, y: number): IPoint {
    return {
      left: x + this.dragOffset.left,
      top: y + this.dragOffset.top,
    };
  }

  private setDOMFixedPosition(position: IOptRect = this.position): void {
    if (this.sourceArea && this.sourceArea === this.targetArea) {
      setStyle(this.dom, { position: "fixed", ...getRectStyle(position) });
    } else {
      setStyle(this.dom, { position: "fixed", ...getRectStyle(position), zIndex: 999 });
    }
  }

  private setDOMRelativePosition(position: IOptRect = this.position): void {
    const { top, left, width, height } = getRectStyle(position);
    setStyle(this.dom, { position: "absolute", top, left, width, height, zIndex: 0 });
  }

  private clearDOMPosition(): void {
    setStyle(this.dom, {
      position: "",
      top: this.initialPosition.top || "",
      left: this.initialPosition.left || "",
      width: this.initialPosition.width || "",
      height: this.initialPosition.height || "",
      zIndex: "",
    });
  }

  private getRelativePoints(area: DragArea): IPoint {
    const itemRect: IPoint = this.getBoundingClientRect();
    const areaRect: IPoint = area.getBoundingClientRect();
    return {
      left: itemRect.left - areaRect.left,
      top: itemRect.top - areaRect.top,
    };
  }

  private setPosition(position: IOptRect): void {
    this.position = { ...this.position, ...position };
  }

  private static getFixedOffset(position: IRect, x: number, y: number): IPoint {
    return {
      left: position.left - x,
      top: position.top - y,
    };
  }

  private getCalculatedPosition(position: IRect): IRect {
    if (this.targetArea === undefined) {
      return position;
    }

    const { grid, constrain }: { grid?: IDimension, constrain?: { margin: number; } | boolean } = this.targetArea.getConstraints();

    if (grid !== undefined && grid.width > 0 && grid.height > 0) {
      position = DragItem.snapToFixedGrid(position, this.targetArea.getBoundingClientRect(), grid, true);
    }

    if (constrain !== undefined) {
      const areaRect: IRect = this.targetArea.getBoundingClientRect();
      position = { ...position, ...DragItem.withinArea(position, areaRect, typeof constrain === "object" ? constrain.margin : undefined) };
    }

    return position;
  }

  private onClick(event: IEvent): void {
    if (Date.now() < this.pressStamp + mouseEventDelay) {
      exec(this.onclick, event, this);
    }
  }

  private onDrag(event: IEvent): void {
    this.dragStarted = true;

    this.setInitialPosition();

    this.previousFixedPosition = this.getBoundingClientRect();
    this.sourceArea = this.getSourceArea();

    if (this.placeholder) {
      if (!this.placeholderPosition) {
        const { width, height } = this.previousFixedPosition;
        this.placeholderPosition = { width, height };
      }
      setStyle(this.placeholder, getRectStyle(this.placeholderPosition));
    }

    const { x, y } = getClientXY(event);
    this.dragOffset = DragItem.getFixedOffset(this.previousFixedPosition, x, y);

    exec(this.onitemmovestart, event, this, this.sourceArea, this.targetArea);

    this.dom.classList.add("dragging");
  }

  private onDragMove(event: IEvent): void {
    this.lastTargetArea = this.targetArea;
    this.targetArea = this.getTargetArea();

    const disable = (area: DragArea) => {
      area.setDisabled(true);
      area.setFocus(false);
      this.preventDrop = true;
      this.dom.classList.remove("within-area");
    };

    const enable = (area: DragArea) => {
      area.setFocus(true);
      area.setDisabled(false);
      this.preventDrop = false;
      this.dom.classList.add("within-area");
    };

    if (this.targetArea) {
      try {
        const hoverResult = this.targetArea.onItemHover(event, this, this.sourceArea, (this.targetArea !== this.lastTargetArea && this.targetArea !== this.sourceArea));
        if (hoverResult === false) {
          disable(this.targetArea);
        } else {
          enable(this.targetArea);
        }
      } catch (err) {
        logger(err);
        disable(this.targetArea);
      }
    }

    let position: IRect = { ...this.previousFixedPosition, ...this.staticPosition };
    const { x, y } = getClientXY(event);
    position = { ...position, ...this.getCursorOffset(x, y) };

    if (this.preventDrop !== true) {
      position = this.getCalculatedPosition(position);
    }

    this.setDOMFixedPosition(position);

    if (this.placeholder) {
      setStyle(this.placeholder, { display: "" });
    }

    exec(this.onitemmove, event, this, this.sourceArea, this.targetArea);
  }

  private onDragUp(event: IEvent): void {
    this.dragStarted = false;

    getAreas(this.type).forEach((area: DragArea) => {
      area.setFocus(false);
      area.setDisabled(false);
    });

    this.staticPosition = {};

    if (this.placeholder) {
      setStyle(this.placeholder, { display: "none" });
    }
    if (this.targetArea && this.preventDrop === true) {
      this.preventDrop = false;
      if (this.sourceArea) {
        this.sourceArea.onItemRemove(event, this);
      } else {
        this.reset(true);
      }
      return;
    }

    const position: IRect = this.getBoundingClientRect();

    if (this.targetArea) {
      this.setPosition({ ...position, ...this.getRelativePoints(this.targetArea), ...this.staticPosition });
      this.setDOMRelativePosition(this.position);
      exec(this.onitemmoveend, event, this, this.sourceArea, this.targetArea);
    }

    if (this.targetArea && this.targetArea !== this.sourceArea) {
      this.targetArea.onItemDrop(event, this, this.sourceArea);
    }

    if (this.sourceArea) {
      if (this.sourceArea !== this.targetArea) {
        this.sourceArea.onItemRemove(event, this);
      }
    } else {
      this.clearDOMPosition();
    }

    this.dom.classList.remove("dragging", "within-area");
  }

  private onResize(side: string): void {
    this.resizeStarted = true;

    this.previousFixedPosition = this.getBoundingClientRect();
    this.sourceArea = this.targetArea = this.getSourceArea();

    this.resizeSide = side;

    exec(this.onitemresizestart, event, this, this.sourceArea);

    this.dom.classList.add("resizing");
  }

  private onResizeMove(x: number, y: number): void {
    if (!this.sourceArea) {
      return;
    }

    const areaRect = this.sourceArea.getBoundingClientRect();

    let position: IRect = { ...this.position as IRect };

    const diff = { left: 0, top: 0, right: 0, bottom: 0 };

    if (this.resizeSide === "left") {
      diff.left = x - this.previousFixedPosition.left;
      const diffLeft = this.position.left + diff.left;
      if (diffLeft < 0) {
        diff.left -= diffLeft;
      }
      position.left = this.position.left + diff.left;
      position.width = this.position.width - diff.left;
    } else if (this.resizeSide === "top") {
      diff.top = y - this.previousFixedPosition.top;
      const diffTop = this.position.top + diff.top;
      if (diffTop < 0) {
        diff.top -= diffTop;
      }
      position.top = this.position.top + diff.top;
      position.height = this.position.height - diff.top;
    } else if (this.resizeSide === "right") {
      diff.right = x - this.position.width - this.previousFixedPosition.left;
      const diffRight = position.left + this.position.width + diff.right;
      if (diffRight > areaRect.width) {
        diff.right += areaRect.width - diffRight;
      }
      position.width = this.position.width + diff.right;
    } else if (this.resizeSide === "bottom") {
      diff.bottom = y - this.position.height - this.previousFixedPosition.top;
      const diffBottom = position.top + this.position.height + diff.bottom;
      if (diffBottom > areaRect.height) {
        diff.bottom += areaRect.height - diffBottom;
      }
      position.height = this.position.height + diff.bottom;
    }

    const { grid }: { grid?: IDimension } = this.sourceArea.getConstraints();

    if (grid !== undefined && grid.width > 0 && grid.height > 0) {
      position = DragItem.snapToRelativeGrid(position, grid);
    }

    this.setDOMRelativePosition(position);

    exec(this.onitemresize, event, this, this.sourceArea);
  }

  private onResizeUp(event: IEvent): void {
    this.resizeStarted = false;

    const position: IRect = this.getBoundingClientRect();

    this.setPosition({ ...position, ...(this.targetArea ? this.getRelativePoints(this.targetArea) : {}) });

    exec(this.onitemresizeend, event, this, this.sourceArea);

    this.dom.classList.remove("resizing");
  }

  private onMouseUp(event: IEvent): void {
    if (!this.dragStarted && !this.resizeStarted) {
      return;
    }

    if (Date.now() < this.pressStamp + mouseEventDelay) {
      this.dragStarted = false;
      this.resizeStarted = false;
      return;
    }

    if (this.dragStarted === true) {
      this.onDragUp(event);
    } else if (this.resizeStarted === true) {
      this.onResizeUp(event);
    }
  }

  private onMouseMove(event: IEvent): void {
    if (Date.now() < this.pressStamp + mouseEventDelay) {
      return;
    }

    if (this.dragStarted) {
      this.onDragMove(event);
    } else if (this.resizeStarted) {
      const { x, y } = getClientXY(event);
      this.onResizeMove(x, y);
    }
  }
}



