import * as m from "mithril";
import { IEvent, IOptRect, IRect } from "../../base/types/index";
import { IContent, IHolder } from "../../base/types/product";
import { devent, getRectStyle, getScaleFromFirstContainer, setStyle } from "../../base/utils/dom";
import { setScale } from "../../base/utils/helpers";
import { cleanString, exec } from "../../base/utils/object";
declare var require: any;
// tslint:disable-next-line:no-var-requires
const iconText = require("../../base/assets/icon-text.png");
// tslint:disable-next-line:no-var-requires
const iconImage = require("../../base/assets/icon-image.png");
// tslint:disable-next-line:no-var-requires
const iconShape = require("../../base/assets/icon-shape.png");
import { IMAGE, SHAPE, TEXT } from "../../base/enums/index";
import "./style.scss";

export interface ILayoutAttrs {
  position: IRect;
  onclick?: (event: IEvent) => void;
  selected?: boolean;
  holders: IHolder[];
}

const getPlaceholderContentIcon = (holder: IHolder) => {
  const icons: any = [];
  if (holder.type.indexOf(IMAGE) !== -1) {
    icons.push(<div><img src={iconImage} alt="icon-image" /></div>);
  }
  if (holder.type.indexOf(TEXT) !== -1) {
    icons.push(<div><img src={iconText} alt="icon-text" /></div>);
  }
  if (holder.type.indexOf(SHAPE) !== -1) {
    icons.push(<div><img src={iconShape} alt="icon-shape" /></div>);
  }
  return <div>{icons}</div>;
};

export default class Layout implements m.ClassComponent<ILayoutAttrs> {
  public view(vnode: m.CVnode<ILayoutAttrs>) {
    return <div className={cleanString("component--LibraryItemLayout", vnode.attrs.selected && "selected")}
      onclick={(event: IEvent) => exec(vnode.attrs.onclick, devent(event))}
    >
      {vnode.attrs.holders && vnode.attrs.holders.map((h: IHolder) =>
        <div className="holder">
          {getPlaceholderContentIcon(h)}
        </div>,
      )}
    </div>;
  }

  public oncreate(vnode: m.CVnodeDOM<ILayoutAttrs>) {
    this.updateScale(vnode.dom, vnode.attrs.position, vnode.attrs.holders);
  }

  public onupdate(vnode: m.CVnodeDOM<ILayoutAttrs>) {
    this.updateScale(vnode.dom, vnode.attrs.position, vnode.attrs.holders);
  }

  private updateScale(dom: Element, position: IRect, holders: IHolder[]) {
    const scale = getScaleFromFirstContainer(dom, "items", position);
    const scaled: IOptRect = setScale(position, scale);
    const { width, height } = getRectStyle(scaled);
    setStyle(dom as HTMLElement, { width, height });
    const children = Array.from(dom.children) as HTMLElement[];
    children.forEach((child, i) => setStyle(child, getRectStyle(setScale(holders[i].position, scale))));
  }
}



