import * as m from "mithril";
import Library from "../../base/components/Library/index";
import { getInnerRect, getRectStyle } from "../../base/utils/dom";
import { getScale } from "../../base/utils/helpers";
import Holder from "../../productComponents/Holder";
import Controller from "./controller";
import "./style.scss";

export interface IHolderDrawerAttrs {
  className?: string;
}

export default class HolderDrawer implements m.ClassComponent<IHolderDrawerAttrs> {
  private itemsContainer: HTMLElement;
  private controller = new Controller();

  public view(v: m.CVnode<IHolderDrawerAttrs>) {
    const { holders } = this.controller.refresh();

    return <div className={`component--HolderDrawer ${v.attrs.className || ""}`}>
      <span className="element-description">Used Holders</span>
      <Library className="defaultLib" oncontainercreate={(container) => this.itemsContainer = container}>
        {holders.map((holder, index) => {
          const scale = this.itemsContainer ? getScale(holder.position, getInnerRect(this.itemsContainer)) : 1;
          holder = this.controller.getScaledHolder(holder, scale);

          return <div key={holder.id} style={getRectStyle({ width: holder.position.width, height: holder.position.height })} >
            <Holder className={holder.selected ? "selected" : ""} holder={holder} onclick={() => this.controller.setCurrentHolder(holder)} thumb={true}/>
          </div>;
        })}
      </Library>
    </div>;
  }
}



