const cache = {};

export const spreadHexRange = (start: string, end: string): string[] => {
  const list: string[] = [];
  for (let i: number = parseInt(start, 16); i <= parseInt(end, 16); i++) {
    list.push(i.toString(16));
  }
  return list;
};

export const createStylesheet = (id: string, content: string) => {
  let styleElement = document.getElementById(id) as HTMLStyleElement;
  if (styleElement) {
    document.getElementsByTagName("head")[0].removeChild(styleElement);
  }
  styleElement = document.createElement("style");
  styleElement.type = "text/css";
  styleElement.id = id;
  styleElement.innerHTML = content;
  document.getElementsByTagName("head")[0].appendChild(styleElement);
};

export const generateIconStylesheet = (unicodes: string[]) => {
  let stylesheet = "";
  unicodes.forEach((unicode) => {
    stylesheet += `.icon-${unicode}:before{content:'\\${unicode}'}`;
  });
  return stylesheet;
};

export const setIcons = (font: string, range: string[]) => {
  if (cache[font] === undefined) {
    cache[font] = spreadHexRange(range[0], range[1]);
    createStylesheet(font, generateIconStylesheet(cache[font]));
  }
};

export const getIconClassNames = (font: string) => cache[font].map((unicode) => `icon-${unicode}`);

export const getIcons = (font: string) => cache[font];



