import * as m from "mithril";
import { IEvent } from "../../types";
import { exec } from "../../utils/object";
import "./style.scss";

export type EventCb = (value: string, event: IEvent, setValue: (value: string) => void) => void;

export interface ICheckboxAttrs {
  className?: string;
  defaultChecked?: boolean;
  label?: string;
  style?: object;
  onclick?: EventCb;
}

export default class Checkbox implements m.ClassComponent<ICheckboxAttrs> {
  public view(v: m.CVnode<ICheckboxAttrs>) {
    const id = Math.random().toString();
    return <div className={"component--Checkbox" + (v.attrs.className || "")} style={v.attrs.style} >
      <input id={id} type="checkbox"
        checked={v.attrs.defaultChecked}
        onclick={(evt: IEvent) => this.emitEvent(v.attrs.onclick, evt)}
      ></input>
      {v.attrs.label && <label htmlFor={id}>{v.attrs.label}</label>}
    </div >;
  }

  private emitEvent(eventCb: EventCb | undefined, event: IEvent): void {
    event.redraw = false;
    exec(eventCb, event.target.checked, event, (checked) => event.target.checked = checked);
  }
}



