import EasyDeps from "easydeps";
import * as m from "mithril";
import ProductManager from "../base/productManager";
import { ILayout, IPrintedResult, RenderedHolders } from "../base/types/product";
import { generateIconStylesheet, getIcons } from "../base/utils/iconfont";
import { domToImage, renderServer } from "../base/utils/image";
import Holder from "./Holder";
import Layout from "./Layout";
import { IDimension } from "../base/types";

let preview;

const initializePreview = () => {
  if (preview !== undefined) {
    return preview;
  }
  preview = document.createElement("div");
  preview.style.position = "fixed";
  preview.style.top = "0";
  preview.style.left = "0";
  preview.style.zIndex = "-100";
  preview.style.pointerEvents = "none";
  document.body.appendChild(preview);
  return preview;
};

const getProductManager = (): ProductManager => {
  return EasyDeps.getInstance().request(["productManager"]) [0];
};

const renderServerHTML = async (html: string, dimensions: IDimension) => renderServer(`
<!DOCTYPE html>
<html lang="en">
<head>
  <meta charset="UTF-8">
  <style>
  @font-face {
    font-family: 'Amatic SC';
    font-style: normal;
    font-weight: 400;
    src: local('Amatic SC Regular'), local('AmaticSC-Regular'), url(https://fonts.gstatic.com/s/amaticsc/v11/DPPfSFKxRTXvae2bKDzp5FtXRa8TVwTICgirnJhmVJw.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
  }
  @font-face {
    font-family: 'Bungee';
    font-style: normal;
    font-weight: 400;
    src: local('Bungee'), local('Bungee-Regular'), url(https://fonts.gstatic.com/s/bungee/v3/86NbnFpNqZ2MU9Gl0ca2YQ.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
  }
  @font-face {
    font-family: 'Indie Flower';
    font-style: normal;
    font-weight: 400;
    src: local('Indie Flower'), local('IndieFlower'), url(https://fonts.gstatic.com/s/indieflower/v9/10JVD_humAd5zP2yrFqw6ugdm0LZdjqr5-oayXSOefg.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
  }
  @font-face {
    font-family: 'Lobster';
    font-style: normal;
    font-weight: 400;
    src: local('Lobster Regular'), local('Lobster-Regular'), url(https://fonts.gstatic.com/s/lobster/v20/cycBf3mfbGkh66G5NhszPQ.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
  }
  @font-face {
    font-family: 'Open Sans Condensed';
    font-style: normal;
    font-weight: 300;
    src: local('Open Sans Condensed Light'), local('OpenSansCondensed-Light'), url(https://fonts.gstatic.com/s/opensanscondensed/v12/gk5FxslNkTTHtojXrkp-xBEur64QvLD-0IbiAdTUNXE.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
  }
  @font-face {
    font-family: 'Schoolbell';
    font-style: normal;
    font-weight: 400;
    src: local('Schoolbell Regular'), local('Schoolbell-Regular'), url(https://fonts.gstatic.com/s/schoolbell/v8/BSqn7FernLolrt-MFco9Wvk_vArhqVIZ0nv9q090hN8.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
  }
  @font-face {
    font-family: 'Shadows Into Light';
    font-style: normal;
    font-weight: 400;
    src: local('Shadows Into Light'), local('ShadowsIntoLight'), url(https://fonts.gstatic.com/s/shadowsintolight/v7/clhLqOv7MXn459PTh0gXYFK2TSYBz0eNcHnp4YqE4Ts.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
  }
  @font-face {
    font-family: 'Material-Design-Iconic-Font';
    font-style: normal;
    font-weight: 400;
    src: url(https://cdnjs.cloudflare.com/ajax/libs/material-design-iconic-font/2.2.0/fonts/Material-Design-Iconic-Font.woff2) format('woff2');
  }
  ${generateIconStylesheet(getIcons("Material-Design-Iconic-Font"))}
  </style>
</head>
<body>
  ${html}
</body>
</html>`, dimensions);

export default async (scale: number = 1, options: { individual?: boolean } = {}): Promise<IPrintedResult> => {
  const manager = getProductManager();
  initializePreview();
  let layout = manager.getProductPart().layout as ILayout;
  if (!layout) { throw new Error("ProductPart is undefined or has no layout"); }
  layout = manager.getScaledLayout(manager.getProductPart().layout, scale);

  let holders: RenderedHolders | undefined = undefined;

  if (options.individual) {
    holders = [];
    for (const h of layout.holders) {
      const position = { left: 0, top: 0, width: h.position.width, height: h.position.height };
      m.render(preview, <Holder holder={h} position={position} />);
      holders.push({ id: h.id, image: await domToImage(preview) });
    }
  }

  m.render(preview, <Layout layout={layout}>{layout.holders.map((holder) => <Holder holder={holder} />)}</Layout>);

  const rect = preview.getBoundingClientRect();
  const result = await renderServerHTML(preview.outerHTML, { width: rect.width, height: rect.height });

  m.render(preview, null);
  return { result, holders };
};



