import EventAggregator from "eventaggregator";
import * as m from "mithril";
import DragItem from "../../base/components/Drag/Item/index";
import Checkbox from "../../base/components/Form/Checkbox";
import ImageUpload from "../../base/components/ImageUpload/index";
import ImageUploadUrl from "../../base/components/ImageUploadUrl";
import Library from "../../base/components/Library/index";
import { IMAGE } from "../../base/enums";
import { IEvent } from "../../base/types/index";
import * as LocalStorage from "../../base/utils/localStorage";
import logger from "../../base/utils/logger";
import T from "../../base/utils/mHelpers/injectTransition";
import "./style.scss";

const TDragItem = T(DragItem, { group: "ImageDrawer", delay: 100, transition: "t--slide-down-bounce" });

interface IImage {
  name: string;
  src: string;
  thumb: string;
}

export interface IImageDrawerAttrs {
  className?: string;
}

export default class ImageDrawer implements m.ClassComponent<IImageDrawerAttrs> {
  private persistent: boolean = false;
  private itemsContainer: HTMLElement;
  private storedImages: IImage[] = [];

  public oncreate(v: m.CVnodeDOM<IImageDrawerAttrs>) {
    EventAggregator.getInstance("images").on("add", (images) => {
      images.forEach((image) => {
        if (this.storedImages.findIndex((storedImage) => storedImage.name === image.name) === -1) {
          this.storedImages.push(image);
        }
      });
      m.redraw();
    });
  }

  public view(v: m.CVnode<IImageDrawerAttrs>) {
    return <div className={`component--ImageDrawer ${v.attrs.className || ""}`}>
      <span className="element-description">Images</span>
      <Library className="defaultLib pixelated" oncontainercreate={(container) => this.itemsContainer = container}>
        {this.storedImages.map((image) => {
          const content = { value: image.src, type: IMAGE, thumb: image.thumb };

          return <TDragItem
            key={image.name}
            data={{ content, imageName: image.name }}
            keepPlaceholder={true}
            preventPosition={true}
            preventResize={true}>
            <img src={image.thumb} alt={image.name} />
          </TDragItem>;
        })}
      </Library>
      <ImageUpload preview={false} quality={.75} multiple={true} thumb={256} max={1400}
        onupload={(event: IEvent, images: IImage[]) => {
          this.setStoredImages(LocalStorage.add("images", images, this.persistent));
        }}
      />
      <div style={{ marginTop: "5px", width: "200px" }}>
        <ImageUploadUrl quality={1} thumb={256} max={1400}
          onupload={(event: IEvent, image: IImage) => {
            this.setStoredImages(LocalStorage.add("images", [image], this.persistent));
          }} />
      </div>
      <Checkbox defaultChecked={this.persistent} label="Save images in LocalStorage"
        onclick={(checked) => {
          this.persistent = checked;
          if (this.persistent) {
            this.setStoredImages(LocalStorage.get("images", this.persistent));
          }
        }} />
    </div>;
  }

  private setStoredImages(storedImages: IImage[]) {
    if (Array.isArray(storedImages)) {
      this.storedImages = storedImages;
      m.redraw();
    }
  }
}



