import * as clone from "clone";
import EventAggregator, { debounce } from "eventaggregator";
import { IMAGE, SHAPE, TEXT } from "./enums";
import { IDimension, IOptRect, IPoint, IRect } from "./types";
import {
  IContent,
  IContentDefinition,
  IContentMeta,
  IContentMetaOptions,
  IHolder,
  IHolderDefinition,
  IHolderUpdate,
  ILayout,
  IPrintedResult,
  IProduct,
  IProductPart,
  IProductPartDefinition,
  IProductResult,
} from "./types/product";
import { round, setScale } from "./utils/helpers";

const productEA: EventAggregator = EventAggregator.getInstance("product");

export default class ProductManager {
  private id: string;
  private productParts: IProductPart[];
  private currentProductPartIndex: number = 0;
  private currentHolderId?: number;
  private currentLayoutId?: string;
  private nextHolder_id: number = 0;
  private debouncer: Function;
  private previewScale: number = 1;
  private defaults?: IContentMeta;

  public constructor(product: IProduct, defaults: IContentMeta | undefined = product.defaults) {
    this.id = product.id;
    this.defaults = defaults;
    this.productParts = clone(product.productParts);
    const pp = this.productParts[this.currentProductPartIndex];
    if (pp !== undefined && pp.layout !== undefined) {
      this.currentLayoutId = pp.layout.id;
    }
  }

  public loadProductPartLayout(productPartId: string, layout: ILayout): void {
    const currentProductPart = this.getProductPartById(productPartId);
    this.setCurrentLayout(layout.id);
    currentProductPart.layout = clone(layout);
    this.changed();
  }

  public getProductParts(): IProductPart[] {
    return clone(this.productParts, false);
  }

  public getProductPartById(productPartId: string): IProductPart {
    const productPart = this.productParts.find((pp) => pp.id === productPartId);
    if (productPart === undefined) {
      throw new Error(`ProductPart with id "${productPartId}" does not exist`);
    }
    return clone(productPart, false);
  }

  public getProductPart(productPartIndex?: number): IProductPart {
    return clone(this.getThisProductPart(productPartIndex), false);
  }

  public getContentOptions(productPartIndex?: number): IContentMetaOptions {
    return clone(this.getThisContentOptions(productPartIndex), false);
  }

  // public getLayout(productPartIndex?: number): ILayout {
  //   return this.getProductPart(productPartIndex).layout;
  // }

  public getLayouts(productPartIndex?: number): ILayout[] {
    return clone(this.getThisLayouts(productPartIndex), false);
  }

  public getLayout(layoutId?: string, productPartIndex?: number): ILayout {
    return clone(this.getThisLayout(layoutId, productPartIndex), false);
  }

  public getHolders(productPartIndex?: number): IHolder[] {
    return clone(this.getThisHolders(productPartIndex), false);
  }

  public getHolder(holderId?: number, productPartIndex?: number): IHolder {
    return clone(this.getThisHolder(holderId, productPartIndex), false);
  }

  public getScaledHolder(holder: IHolder, scale: number = this.previewScale): IHolder {
    return this.scaleHolder(clone(holder, false) as IHolder, scale);
  }

  public getScaledLayout(layout: ILayout, scale: number = this.previewScale): ILayout {
    return this.scaleLayout(clone(layout, false) as ILayout, scale);
  }

  public addHolder(holder?: IHolder, productPartIndex?: number): IHolder {
    const productPart = this.getThisProductPart(productPartIndex);
    const nextId = this.getNextHolderId(productPart.layout.holders);
    if (holder === undefined) {
      holder = {
        id: nextId,
        position: {
          top: 0,
          left: 0,
          width: productPart.layout.position.width,
          height: productPart.layout.position.height,
        },
        fixed: false,
        type: [TEXT, IMAGE, SHAPE],
      };
    } else {
      holder.id = nextId;
    }
    productPart.layout.holders.push(clone(holder, false));
    this.changed();
    return holder;
  }

  public updateHolder(holder: IHolderUpdate, productPartIndex?: number): IHolder {
    const layout = this.getThisProductPart(productPartIndex).layout;
    const exitstingHolder = layout.holders.find((h) => h.id === holder.id);
    if (exitstingHolder === undefined) {
      throw new Error(`Could not find an existing holder with id "${holder.id}"`);
    }
    if (layout.fixed !== true) {
      if (exitstingHolder.fixed !== true) {
        if (holder.position !== undefined) {
          this.updateHolderPosition(exitstingHolder, holder.position);
        }
      }
      if (holder.depth !== undefined) {
        this.updateHolderDepth(exitstingHolder, holder.depth, layout.holders);
      }
    }
    if (holder.content !== undefined) {
      // if (exitstingHolder.type === holder.content.type || (Array.isArray(exitstingHolder.type) && exitstingHolder.type.indexOf(holder.content.type) !== -1)) {
      this.updateHolderContent(exitstingHolder, holder.content);
      // }
    }
    this.changed();
    return clone(exitstingHolder, false);
  }

  public removeHolderContent(holderId: number, productPartIndex?: number): IHolder {
    const holders = this.getThisProductPart(productPartIndex).layout.holders;
    const exitstingHolder = holders.find((h) => h.id === holderId);
    if (exitstingHolder === undefined) {
      throw new Error(`Could not find an existing holder with id "${holderId}"`);
    }
    if (exitstingHolder.fixed && exitstingHolder.content) {
      exitstingHolder.content = {} as IContent; // type: exitstingHolder.content.type
    } else {
      exitstingHolder.content = undefined;
    }
    this.changed();
    return exitstingHolder;
  }

  public removeHolder(holderId: number, productPartIndex?: number): void {
    const layout = this.getThisProductPart(productPartIndex).layout;
    const exitstingHolder = layout.holders.find((h) => h.id === holderId);
    if (exitstingHolder === undefined) {
      throw new Error(`Could not find an existing holder with id "${holderId}"`);
    }
    if (layout.fixed !== true && exitstingHolder.fixed !== true) {
      layout.holders = layout.holders.filter((holder) => holder.id !== holderId);
    }

    if (holderId === this.currentHolderId) {
      this.currentHolderId = -1;
      this.updateSelected();
    }
    this.changed();
  }

  public setCurrentProductPart(productPartId?: string): void {
    this.currentProductPartIndex = this.productParts.findIndex((pp: IProductPart) => pp.id === productPartId);
    this.updateSelected();
    this.changed();
  }

  public setCurrentHolder(holderId?: number, productPartIndex?: number): void {
    if (this.currentHolderId === holderId) {
      holderId = undefined;
    }
    this.currentHolderId = holderId;
    this.updateSelected();
    this.changed();
  }

  public setCurrentLayout(layoutId: string, productPartIndex?: number): void {
    const pp = this.getThisProductPart(productPartIndex);
    this.currentLayoutId = layoutId;
    const layout = this.getThisLayout(layoutId);
    pp.layout = clone(layout, false);
    this.updateSelected();
    this.changed();
  }

  public addLayout(layout: ILayout, productPartIndex?: number): ILayout {
    const pp = this.getThisProductPart(productPartIndex);
    pp.layouts.push(clone(layout, false));
    this.changed();
    return pp.layout;
  }

  public setPreviewScale(scale: number) {
    this.previewScale = scale;
    this.changed();
  }

  public getPreviewScale(): number {
    return this.previewScale;
  }

  public submit(result: IPrintedResult, productPartIndex?: number): IProductResult {
    // const productPartDefinition: IProductPartDefinition = {
    //   id: this.id,
    //   productParts: [],
    // };

    const productPart = this.getProductPart(productPartIndex);

    const productPartDefinition: IProductPartDefinition = {
      id: productPart.id,
      layout: {
        id: productPart.layout.id,
        position: productPart.layout.position,
        holders: [],
      },
    };

    productPartDefinition.layout.holders = productPart.layout.holders.map((h) => {
      if (!h.content) {
        return null;
      }

      const holderResult = result.holders && result.holders.find((hr) => hr.id === h.id);
      const holderDefinition: IHolderDefinition = {
        id: h.id,
        type: [...h.type],
        position: { ...h.position },
        content: (() => {
          if (holderResult && holderResult.image) {
            return holderResult.image;
          }
          const cleanedContent = clone(h.content, false);
          delete cleanedContent.fixed;
          delete cleanedContent.originalValue;
          delete cleanedContent.thumb;
          return cleanedContent as IContentDefinition;
        })(),
      };
      return holderDefinition;
    });

    productEA.emit("submit", {
      result: result.result,
      definition: { id: this.id, productPart: productPartDefinition },
    });
    return {
      result: result.result,
      definition: { id: this.id, productPart: productPartDefinition },
    };
  }

  private scale(position: number, scale: number = 1 / this.previewScale): number {
    return scale * position;
  }

  private scaleRect(position: IOptRect, scale: number = 1 / this.previewScale): IOptRect {
    return round(setScale(position, scale)) as IOptRect;
  }

  private updateHolderPosition(holder: IHolder, position: IOptRect): IRect {
    position = this.scaleRect(position, 1 / this.previewScale);
    holder.position = { ...holder.position, ...position };
    return holder.position;
  }

  private updateHolderContent(holder: IHolder, content: IContent): IContent {
    delete content.originalValue;
    if (holder.type.indexOf(IMAGE) !== -1 && content.type === IMAGE) {
      if (!holder.content || holder.content.originalValue === undefined) {
        content.originalValue = content.value;
      }
    }
    holder.content = { ...this.defaults, ...holder.content, ...content };
    return holder.content;
  }

  private updateHolderDepth(holder: IHolder, direction: number, holders: IHolder[]): IHolder[] {
    const holderIndex = holders.indexOf(holder);
    if (direction > 0) {
      const prevHolder = holders[holderIndex + 1];
      if (prevHolder) {
        holders[holderIndex + 1] = holder;
        holders[holderIndex] = prevHolder;
      }
    } else {
      const prevHolder = holders[holderIndex - 1];
      if (prevHolder) {
        holders[holderIndex - 1] = holder;
        holders[holderIndex] = prevHolder;
      }
    }
    return holders;
  }

  private updateSelected(): void {
    this.productParts.forEach((pp, ppi) => {
      pp.selected = ppi === this.currentProductPartIndex;
      // pp.holders.forEach((h, hi) => {
      //   h.selected = h.id === this.currentHolderId;
      // });
      pp.layouts.forEach((t, ti) => {
        t.selected = t.id === this.currentLayoutId;
      });
      if (pp.layout !== undefined) {
        if (pp.layout.holders !== undefined) {
          pp.layout.holders.forEach((h, hi) => {
            h.selected = h.id === this.currentHolderId || h.id === this.currentHolderId;
          });
        }
      }
    });
  }

  private changed() {
    if (this.debouncer === undefined) {
      this.debouncer = debounce(() => productEA.emit("change", this.getProductPart()), 50);
    }
    this.debouncer();
  }

  private getNextHolderId(holders?: IHolder[]): number {
    if (holders && holders.length > 0) {
      this.nextHolder_id = Math.max.apply(null, holders.map((holder) => holder.id)) + 1;
    }
    return this.nextHolder_id++;
  }

  private scaleHolder(holder: IHolder, scale: number): IHolder {
    holder.position = this.scaleRect(holder.position, scale) as IRect;
    if (holder.content) {
      if (holder.content.fontSize) {
        holder.content.fontSize = this.scale(parseFloat(holder.content.fontSize), scale) + "px";
      }
    }
    return holder;
  }

  private scaleLayout(layout: ILayout, scale: number): ILayout {
    layout.position = this.scaleRect(layout.position, scale) as IRect;
    layout.holders = layout.holders.map((holder) => this.scaleHolder(holder, scale));
    return layout;
  }

  private getThisProductParts(): IProductPart[] {
    return this.productParts;
  }

  private getThisProductPartById(productPartId: string): IProductPart {
    const productPart = this.productParts.find((pp) => pp.id === productPartId);
    if (productPart === undefined) {
      throw new Error(`ProductPart with id "${productPartId}" does not exist`);
    }
    return productPart;
  }

  private getThisProductPart(productPartIndex: number = this.currentProductPartIndex): IProductPart {
    const pp = this.productParts[productPartIndex];
    if (pp === undefined) {
      throw new Error(`ProductPart with index "${productPartIndex}" does not exist`);
    }
    return pp;
  }

  private getThisContentOptions(productPartIndex: number = this.currentProductPartIndex): IContentMetaOptions {
    return this.productParts[productPartIndex].options;
  }

  private getThisLayouts(productPartIndex: number = this.currentProductPartIndex): ILayout[] {
    return this.getThisProductPart(productPartIndex).layouts;
  }

  private getThisLayout(layoutId: string | undefined = this.currentLayoutId, productPartIndex: number = this.currentProductPartIndex): ILayout {
    const layouts = this.getThisProductPart(productPartIndex).layouts || [];
    const layout = layouts.find((l) => l.id === layoutId);
    if (layout === undefined) {
      throw new Error(`ProductPart with index "${productPartIndex}" has no layout with id "${layoutId}"`);
    }
    return layout;
  }

  private getThisHolders(productPartIndex: number = this.currentProductPartIndex): IHolder[] {
    return this.getThisProductPart(productPartIndex).layout.holders;
  }

  private getThisHolder(holderId: number | undefined = this.currentHolderId, productPartIndex: number = this.currentProductPartIndex): IHolder {
    if (holderId === undefined) {
      throw new Error(`HolderId is undefined. Initialize the current holder with setCurrentHolder(holderId: number)`);
    }
    const holders = this.getThisHolders(productPartIndex);
    const holder = holders.find((h) => h.id === holderId);
    if (holder === undefined) {
      throw new Error(`ProductPart with index "${productPartIndex}" has no holder with id "${holderId}"`);
    }
    return holder;
  }
}



