import * as m from "mithril";
import { devent } from "../../utils/dom";
import { compress, getThumb, uploadImage } from "../../utils/image";
import logger from "../../utils/logger";
import { exec } from "../../utils/object";

export interface IImage {
  name: string;
  src: string;
  thumb?: string;
}

export type Onupload = (event, images: IImage[] | IImage) => void;

export interface IImageUploadAttrs {
  onupload: Onupload;
  className?: string;
  preview?: boolean;
  quality?: number;
  multiple?: boolean;
  max?: number | number[];
  thumb?: number | number[];
  onprogress?: (percentage: number) => void;
}

class ImageUploadController {
  public src: string;
  public name: string;
  private placeholderImage?: ImageData;

  constructor(placeholderImage?: ImageData) {
    this.placeholderImage = placeholderImage;
  }

  public async onUpload(event,
                        callback: (images: IImage) => void,
                        quality?: number,
                        max?: number | number[],
                        thumbmax?: number | number[],
                        preview?: boolean,
                        multiple?: boolean,
                        progress?: (percentage: number) => void): Promise<any> {
    devent(event);

    const maxwidth = max ? (typeof max === "number" ? max : max[0]) : undefined;
    const maxheight = max ? (typeof max === "number" ? max : max[1]) : undefined;

    const thumbwidth = thumbmax ? (typeof thumbmax === "number" ? thumbmax : thumbmax[0]) : undefined;
    const thumbheight = thumbmax ? (typeof thumbmax === "number" ? thumbmax : thumbmax[1]) : undefined;

    let progressPercentage = 0;

    const upload = async (file: File, totalFiles: number = 1): Promise<IImage> => {
      const progressPercentageFraction = 100 / totalFiles / 6;
      const name: string = file.name;
      progressPercentage += progressPercentageFraction;
      exec(progress, progressPercentage);
      const src = await uploadImage(file, quality, maxwidth, maxheight);
      progressPercentage += thumbmax ? (progressPercentageFraction * 3) : (progressPercentageFraction * 5);
      exec(progress, progressPercentage);
      let thumb;
      if (thumbmax) {
        progressPercentage += progressPercentageFraction * 2;
        exec(progress, progressPercentage);
        thumb = await getThumb(src, .5, thumbwidth, thumbheight);
      }
      return { name, src, thumb };
    };

    const files: File[] = event.target.files;
    if (multiple === false) {
      try {
        exec(callback, await upload(files[0]));
      } catch (err) {
        logger(err);
      }
      return;
    }

    const images: IImage[] = [];
    for (const file of files) {
      try {
        images.push(await upload(file, files.length));
      } catch (err) {
        logger(err);
      }
    }
    exec(callback, images);
  }
}

export default class ImageUpload implements m.ClassComponent<IImageUploadAttrs> {
  private progress: number = 0;
  private controller: ImageUploadController = new ImageUploadController();

  public view(v: m.CVnode<IImageUploadAttrs>) {
    return <div className="component--ImageUpload">
      <label><span>Upload{this.progress > 0 ? `ing... ${this.progress.toFixed(2)}%` : ""}</span>
        <input type="file" multiple={v.attrs.multiple} accept=".jpg, .jpeg, .png" onchange={(event) =>
          this.controller.onUpload(event, (images) => {
            exec(v.attrs.onupload, event, images);
            this.progress = 0;
            m.redraw();
          } , v.attrs.quality, v.attrs.max, v.attrs.thumb, v.attrs.preview, v.attrs.multiple, (progress) => {
            exec(v.attrs.onprogress, progress);
            this.progress = progress;
            m.redraw();
          })} />
      </label>
      {v.attrs.preview && this.controller.src && <img src={this.controller.src} alt="preview" />}
    </div>;
  }
}



