import EasyDeps from "easydeps";
import EventAggregator from "eventaggregator";
import * as m from "mithril";
import { IMAGE } from "../../base/enums";
import ProductManager from "../../base/productManager";
import { ILayout } from "../../base/types/product";
import { setIcons } from "../../base/utils/iconfont";
import { downloadImage } from "../../base/utils/image";
import * as LocalStorage from "../../base/utils/localStorage";
// tslint:disable-next-line:ordered-imports
import "../../fonts.scss";
import "../../grid.scss";
import printer from "../../productComponents";
import EditorDrawer from "../EditorDrawer/index";
import HolderDrawer from "../HolderDrawer/index";
import ImageDrawer from "../ImageDrawer/index";
import LayoutDrawer from "../LayoutDrawer/index";
import OtherDrawer from "../OtherDrawer/index";
import ProductPreview from "../ProductPreview/index";
import "./animations.scss";
import "./style.scss";

setIcons("Material-Design-Iconic-Font", ["f101", "f409"]);

const load = () => {
  EasyDeps.getInstance().invokeFrom(["productManager"], (pm: ProductManager) => {
    const pp = pm.getProductPart();
    const layout = LocalStorage.get(`definition_${pp.id}`) as ILayout;
    const images = layout.holders
      .map((holder) => (holder.content && holder.content.type === IMAGE && holder.content.originalValue && holder.content.thumb) ?
        ({ name: holder.content.thumb.slice(-8), thumb: holder.content.thumb, src: holder.content.originalValue })
        : false)
      .filter((content) => content !== false);
    EventAggregator.getInstance("images").emit("add", images);
    pm.loadProductPartLayout(pp.id, layout);
  });
};

const store = () => {
  EasyDeps.getInstance().invokeFrom(["productManager"], (pm: ProductManager) => {
    const pp = pm.getProductPart();
    LocalStorage.set(`definition_${pp.id}`, pp.layout);
  });
};

const submit = async (scale = 1, individual = false, download = true) => {
  const deps = EasyDeps.getInstance().request(["productManager"]);
  const productManager = deps[0] as ProductManager;
  const result = await printer(scale, { individual });
  const img = new Image();
  img.src = result.result;
  document.body.appendChild(img);
  // if (download) {
  //   await downloadImage(result.result, "product");
    // if (result.holders) {
    //   for (const holder of result.holders) {
    //     await downloadImage(holder.image, `holderId_${holder.id}`);
    //   }
    // }
  // }
  productManager.submit(result);
};

export default class Main implements m.ClassComponent<any> {
  public view(v: m.CVnode<any>) {
    return <div className="component--Main">
      <button onclick={() => submit(6, true)}>SUBMIT</button>
      <button onclick={() => store()}>STORE</button>
      <button onclick={() => load()}>LOAD</button>
      <div className="sideBySide">
        <LayoutDrawer />
        <HolderDrawer />
      </div>
      <div className="sideBySide">
        <ImageDrawer />
        <OtherDrawer />
      </div>

      <div className="ProductPreview-wrapper"><ProductPreview /></div>

      <EditorDrawer />
    </div>;
  }
}



