import * as m from "mithril";
import { IEvent } from "../../types/index";
import { exec } from "../../utils/object";
import "./style.scss";

export interface ISelectAttrs {
  className?: string;
  value?: string;
  style?: object;
  onchange?: (value: string, event: IEvent) => void;
  oninput?: (value: string, event: IEvent) => void;
}

export default class Select implements m.ClassComponent<ISelectAttrs> {
  private prevOption: any;
  private selectedOption: any;
  private clickedOption: any;
  private dom: Element;

  public oncreate(v: m.CVnodeDOM<ISelectAttrs>) {
    this.dom = v.dom;
  }

  public view(v: m.CVnode<ISelectAttrs>) {
    const children = (Array.isArray(v.children) ? v.children : [v.children]);
    return <div className={`component--Select ${v.attrs.className}`} style={v.attrs.style}
      onclick={(event) => {
        if (this.dom.classList.contains("open")) {
          this.dom.classList.remove("open");
        } else {
          this.dom.classList.add("open");
        }
      }}>
      <div className="value" key={this.prevOption}>{children.find((item) => this.getOption(item) === this.clickedOption) || children[0]}</div>
      <div className="options">
        {children.map((item, index) => {
          if (!item) { return null; }
          const option = this.getOption(item);
          return <div className={`option ${this.isSelected(this.prevOption, item, option) ? "selected" : ""}`}
            onclick={(event) => {
              event.stopPropagation();
              this.dom.classList.remove("open");
              this.prevOption = option;
              this.clickedOption = this.selectedOption;
              exec(v.attrs.oninput, option, event);
            }}
            onmouseenter={(event) => {
              if (this.prevOption === option) {
                event.redraw = false;
                return;
              }
              this.selectedOption = option;
              exec(v.attrs.oninput, option, event);
            }}
            onmouseleave={(event) => {
              if (this.prevOption === option) {
                event.redraw = false;
                return;
              }
              this.selectedOption = undefined;
              exec(v.attrs.oninput, this.prevOption, event);
            }}
          >{item}</div>;
        })}</div>
    </div>;
  }

  private getOption(item: any) {
    return typeof item === "object" && !Array.isArray(item) && item && item.attrs && item.attrs["data-option"];
  }

  private isSelected(test: any, item: any, option?: any): boolean {
    if (option === undefined) {
      option = this.getOption(item);
    }
    return (option && option === test);
  }
}



