import { IDimension, IPoint, IRect } from "../../types";
import { getRectStyle } from "../../utils/dom";
import { getScale } from "../../utils/helpers";

let preview;
export const initializePreview = () => {
  if (preview !== undefined) {
    return preview;
  }
  preview = document.createElement("div");
  preview.style.position = "fixed";
  preview.style.top = "0";
  preview.style.left = "0";
  preview.style.zIndex = "-100";
  preview.style.pointerEvents = "none";
  document.body.appendChild(preview);
  return preview;
};

export const zoomIn = (position: IRect): IRect => {
  const diffW = position.width * .2;
  const diffH = position.height * .2;
  position.left -= diffW / 2;
  position.top -= diffH / 2;
  position.width += diffW;
  position.height += diffH;
  return position;
};

export const zoomOut = (position: IRect): IRect => {
  const diffW = position.width * .2;
  const diffH = position.height * .2;
  position.top += diffH / 2;
  position.left += diffW / 2;
  position.width -= diffW;
  position.height -= diffH;
  return position;
};

export const rotate = (rotation: number, amount: number): number => {
  rotation += amount;
  if (rotation > 360) {
    rotation -= 360;
  } else if (rotation < 0) {
    rotation += 360;
  }
  return rotation;
};

export const zoom = (containerRect: IRect, itemRect: IRect, direction: number): IRect => {
  const nextPos = direction > 0 ? zoomIn(itemRect) : zoomOut(itemRect);
  const relPos = getRelativePosition(nextPos, containerRect);
  return relPos;
};

export const getRelativePosition = (fixedPosition: IRect, parentPosition: IPoint): IRect => {
  const position = { ...fixedPosition };
  position.left -= parentPosition.left;
  position.top -= parentPosition.top;
  return position;
};

export const getContainerStyle = (areaDimension: IDimension): any => ({
  position: "relative",
  width: areaDimension.width + "px",
  height: areaDimension.height + "px",
  overflow: "hidden",
});

export const getImgStyle = (imagePosition: IRect, rotation: number): any => ({
  position: "absolute",
  objectFit: "cover",
  width: "100%",
  height: "100%",
  ...getRectStyle(imagePosition),
  transform: getRotateStyleProp(rotation),
});

export const getRotateStyleProp = (rotation: number) => `rotate(${rotation}deg)`;

export const getAreaScale = (target: IDimension, containerRect: IRect) => getScale(target, { width: containerRect.width * .8, height: containerRect.height * .8 });

export const getScaledItemRect = (containerRect: IRect, areaRect: IRect, itemRect: IRect): IRect => {
  const leftOffset = areaRect.left - containerRect.left;
  const topOffset = areaRect.top - containerRect.top;

  const scale = getScale(itemRect, areaRect, true);
  const width = itemRect.width * scale;
  const height = itemRect.height * scale;

  const left = (width > areaRect.width ? ((areaRect.width - width) / 2) : 0) + leftOffset;
  const top = (height > areaRect.height ? ((areaRect.height - height) / 2) : 0) + topOffset;

  return { top, left, width, height };
};



