import * as m from "mithril";
import { IEvent, IRect } from "../../types/index";
import { devent, getInnerRect } from "../../utils/dom";
import { cleanString, exec } from "../../utils/object";
import Controller from "./controller";
import "./style.scss";

export interface ILibraryAttrs {
  className?: string;
  onclick: (event: IEvent, index: number) => void;
  oncontainercreate: () => Element;
}

export default class Library implements m.ClassComponent<ILibraryAttrs> {
  private c = new Controller();

  public oncreate(vnode: m.CVnodeDOM<ILibraryAttrs>) {
    this.c.construct();
    this.c.refresh();
    exec(vnode.attrs.oncontainercreate, this.c.container);
  }

  public onupdate(vnode: m.CVnodeDOM<ILibraryAttrs>) {
    this.c.refresh();
    exec(vnode.attrs.oncontainercreate, this.c.container);
  }

  public onremove(vnode: m.CVnodeDOM<ILibraryAttrs>) {
    this.c.destruct();
  }

  public view(vnode: m.CVnode<ILibraryAttrs>) {
    const children: m.Children = vnode.children as any[];
    return <div className={cleanString("component--library", vnode.attrs.className)}>
      <div oncreate={(node: m.VnodeDOM<any, any>) => this.c.prev = node.dom} className="nav prev"
        onclick={(event: IEvent) => devent(event) && this.c.navigate(-1)}
      >{"<"}</div>
      <div oncreate={(node: m.VnodeDOM<any, any>) => this.c.container = node.dom} className="items">
        {children.map((child, index) =>
          <div className="item"
            onclick={(event) => exec(vnode.attrs.onclick, devent(event), index)}
          >{child}</div>,
        )}
      </div>
      <div oncreate={(node: m.VnodeDOM<any, any>) => this.c.next = node.dom} className="nav next"
        onclick={(event: IEvent) => devent(event) && this.c.navigate(1)}
      >{">"}</div>
    </div>;
  }
}



