import ProductManager from "../../base/ProductManager";
import { IContentMetaOptions, IHolder, IHolderUpdate, ILayout } from "../../base/types/product";
import EasyDeps from "easydeps";
import logger from "../../base/utils/logger";

export default class EditorDrawerController {
  public scale = 1;
  public layout?: ILayout;
  public holder?: IHolder;
  public options?: IContentMetaOptions;
  private manager: ProductManager;

  constructor() {
    EasyDeps.getInstance().invoke((productManager) => {
      this.manager = productManager;
    });
  }

  public refresh() {
    try {
      this.holder = this.manager.getHolder();
      this.layout = this.manager.getLayout();
      this.options = this.manager.getContentOptions();
      this.scale = this.manager.getPreviewScale();
    } catch (err) {
      logger(err);
      this.holder = undefined;
      this.layout = undefined;
    }
    return {layout: this.layout, holder: this.holder, options: this.options, scale: this.scale};
  }

  public getEditorOptions() {
    return {
      depth: this.layout && this.layout.fixed !== true,
      type: this.holder && this.holder.content && this.holder.content.type,
    };
  }

  public update(update: any) {
    if (!this.holder) {
      logger(new Error("Cannot update the holder. this.holder is undefined"));
      return;
    }
    update.id = this.holder.id;
    this.manager.updateHolder(update);
  }
}



