
import * as m from "mithril";
import { IMAGE, SHAPE, TEXT } from "../base/enums";
import { IHolder } from "../base/types/product";

export interface IHolderContentAttrs {
  holder: IHolder;
  thumb?: boolean;
}

export default class HolderContent implements m.ClassComponent<IHolderContentAttrs> {
  public view(v: m.CVnode<IHolderContentAttrs>) {
    const h = v.attrs.holder;
    if (!h || !h.content || !h.content.value) { return null; }
    if (h.content.type === IMAGE) {
      return <img style={getHolderImgStyle(v.attrs.holder)} src={v.attrs.thumb ? h.content.thumb : h.content.value} />;
      // return <ContentImage holder={h} thumb={v.attrs.thumb} />;
    } else if (h.content.type === TEXT) {
      return <div style={getHolderTextStyle(v.attrs.holder)}>{h.content.value}</div>;
      // return <ContentText holder={h} thumb={v.attrs.thumb} />;
    } else if (h.content.type === SHAPE) {
      return <div style={{ ...getHolderShapeStyle(v.attrs.holder), fontFamily: h.content.fontFamily }} className={"icon-" + h.content.value} />;
      // return <ContentShape holder={h} thumb={v.attrs.thumb} />;
    }
  }
}

const holderContentStyle = (h: IHolder) => {
  if (!h.content) {
    return {};
  }
  let style: any = {
    width: "100%",
  };
  if (h.content.filter && h.content.filter.length > 0) {
    style = {
      ...style, ...h.content.filter.reduce((s, f) => {
        Object.keys(f).forEach((k) => {
          if (s[k]) {
            s[k] += " " + f[k];
          } else {
            s[k] = f[k];
          }
        });
        return s;
      }, {}),
    };
  }
  return style;
};

export const getHolderImgStyle = (h: IHolder) => {
  let style = { ...holderContentStyle(h) };
  if (!h.content) {
    return style;
  }
  style = {
    ...style,
    position: "absolute",
    objectFit: "cover",
    width: "100%",
    height: "100%",
  };
  return style;
};

export const getHolderShapeStyle = (h: IHolder) => {
  let style = { ...holderContentStyle(h) };
  if (!h.content) {
    return style;
  }
  const { color, fontSize, fontWeight, textDecoration } = h.content;
  style = {
    ...style,
    color,
    fontSize,
    textAlign: (h.content && h.content.align) || "",
  };
  return style;
};

export const getHolderTextStyle = (h: IHolder) => {
  let style = { ...holderContentStyle(h) };
  if (!h.content) {
    return style;
  }
  const { color, fontSize, fontFamily, fontStyle, fontWeight, textDecoration } = h.content;
  style = {
    ...style,
    color,
    fontSize,
    fontFamily,
    fontStyle,
    fontWeight,
    textDecoration,
    textAlign: (h.content && h.content.align) || "",
  };
  return style;
};



