import * as m from "mithril";
import { IDimension, IEvent, IRect } from "../../../types";
import { devent, getOuterRect, getRectStyle } from "../../../utils/dom";
import { exec } from "../../../utils/object";
import DragItem from "../Item";
import "./style.scss";

const areas: { [key: string]: DragArea[] } = {};

export const getAreas = (type: string) => areas[type] || [];

export interface IAreaAttrs {
  type: string;
  className?: string;
  onareadrop?: (event: IEvent, item: DragItem, source: DragArea | undefined, target: DragArea) => void;
  onareahover?: (event: IEvent, item: DragItem, source: DragArea | undefined, target: DragArea, initial: boolean) => void | boolean;
  onarearemove?: (event: IEvent, item: DragItem, source: DragArea) => void;
  onclick?: (event: IEvent, source: DragArea) => void;
  grid?: IDimension;
  constrain?: { margin: number } | boolean;
  position?: IRect;
}

export default class DragArea implements m.ClassComponent<IAreaAttrs> {
  private constrain?: { margin: number } | boolean;
  private grid?: IDimension;
  private onareadrop?: (event: IEvent, item: DragItem, source: DragArea | undefined, target: DragArea) => void;
  private onareahover?: (event: IEvent, item: DragItem, source: DragArea | undefined, target: DragArea, initial: boolean) => void;
  private onarearemove?: (event: IEvent, item: DragItem, source: DragArea) => void;
  private onclick?: (event: IEvent, source: DragArea) => void;
  private dom: HTMLElement;
  private items: DragItem[] = [];
  private type: string = "main";

  public getBoundingClientRect(): IRect {
    return getOuterRect(this.dom);
  }

  public setFocus(focus: boolean) {
    return this.dom.classList[focus ? "add" : "remove"]("focus");
  }

  public setDisabled(disabled: boolean) {
    return this.dom.classList[disabled ? "add" : "remove"]("disabled");
  }

  public getItems(): DragItem[] {
    return this.items;
  }

  public withinArea(itemRect: IRect, options: { complete?: boolean; margin?: number } = {}): boolean {
    const areaRect: IRect = this.getBoundingClientRect();
    if (options.complete) {
      return (
        itemRect.left > areaRect.left &&
        itemRect.left + itemRect.width < areaRect.left + areaRect.width &&
        itemRect.top > areaRect.top &&
        itemRect.top + itemRect.height < areaRect.top + areaRect.height
      );
    } else if (options.margin) {
      return (
        itemRect.left + options.margin > areaRect.left &&
        itemRect.left + itemRect.width - options.margin < areaRect.left + areaRect.width &&
        itemRect.top + options.margin > areaRect.top &&
        itemRect.top + itemRect.height - options.margin < areaRect.top + areaRect.height
      );
    } else {
      return (
        itemRect.left + itemRect.width > areaRect.left &&
        itemRect.left < areaRect.left + areaRect.width &&
        itemRect.top + itemRect.height > areaRect.top &&
        itemRect.top < areaRect.top + areaRect.height
      );
    }
  }

  public getConstraints(): { grid?: IDimension; constrain?: { margin: number } | boolean } {
    return {
      grid: this.grid,
      constrain: this.constrain,
    };
  }

  public onItemDrop(event: IEvent, item: any, sourceArea?: DragArea): void {
    return exec(this.onareadrop, devent(event), item, sourceArea, this);
  }

  public onItemHover(event: IEvent, item: any, sourceArea: DragArea | undefined, initial: boolean): void | boolean {
    return exec(this.onareahover, devent(event), item, sourceArea, this, initial);
  }

  public onItemRemove(event: IEvent, item: any): void {
    return exec(this.onarearemove, devent(event), item, this);
  }

  public onbeforeupdate(vnode: m.Vnode<IAreaAttrs, DragArea>, old: m.VnodeDOM<IAreaAttrs, DragArea>) {
    return this.attrsChanged(vnode.attrs, old.attrs);
  }

  public onupdate(vnode: m.VnodeDOM<IAreaAttrs, this>) {
    this.setState(vnode.attrs);
    this.setItems(vnode.children);
  }

  public oninit(vnode: m.Vnode<IAreaAttrs, this>) {
    this.setState(vnode.attrs);
    this.setItems(vnode.children);
  }

  public onremove(vnode: m.Vnode<IAreaAttrs, this>) {
    const dragAreas = getAreas(this.type);
    dragAreas.splice(dragAreas.indexOf(this), 1);
  }

  public oncreate(vnode: m.VnodeDOM<IAreaAttrs, this>) {
    this.dom = vnode.dom as HTMLElement;
    if (areas[this.type] === undefined) {
      areas[this.type] = [];
    }
    areas[this.type].push(this);
  }

  public view(vnode: m.Vnode<IAreaAttrs, this>) {
    return <div
      className={"component--drag drag-area " + (vnode.attrs.className || "")}
      style={getRectStyle(vnode.attrs.position || {})}
      onclick={(event) => exec(this.onclick, devent(event), this)}>
      {vnode.children}
    </div>;
  }

  private attrsChanged(next: IAreaAttrs, prev: IAreaAttrs): boolean {
    return (
      next.className !== prev.className ||
      next.onareadrop !== prev.onareadrop ||
      next.onareahover !== prev.onareahover ||
      next.onarearemove !== prev.onarearemove ||
      next.grid !== prev.grid ||
      next.constrain !== prev.constrain);
  }

  private setState(attrs: IAreaAttrs): void {
    Object.keys(attrs).forEach((key: string) => this[key] = attrs[key]);
  }

  private setItems(children: m.Children) {
    if (Array.isArray(children)) {
      this.items = [];
      children.forEach((v: m.Children) => {
        if (typeof v === "object" && v !== null && Array.isArray(v) === false && v["state"] instanceof DragItem) {
          this.items.push(v["state"]);
        }
      });
    }
  }
}



