import { IHolder, ILayout } from "../../base/types/product";
import { getInnerRect, getRectStyle } from "../../base/utils/dom";
import { getScale, setScale } from "../../base/utils/helpers";
import T from "../../base/utils/mHelpers/injectTransition";
import Holder from "../../productComponents/Holder";
import Controller from "./controller";
import "./style.scss";

