export interface IProductPreviewAttrs {
  layout: ILayout;
  className?: string;
}

const TDragArea = T(DragArea, { group: "ProductPreview", delay: 200, transition: "t--slide-down-bounce" });

export default class ProductPreview implements m.ClassComponent<IProductPreviewAttrs> {
  private controller = new Controller();

  public onremove(v: m.CVnodeDOM<IProductPreviewAttrs>) {
    this.controller.destruct();
  }

  public oncreate(v: m.CVnodeDOM<IProductPreviewAttrs>) {
    this.controller.addEvent("resize", debounce(() => this.updateScale(v.dom), 100));
    this.updateScale(v.dom);
  }

  public view(v: m.CVnode<IProductPreviewAttrs>) {
    const { layout } = this.controller.refresh();

    if (!layout) {
      return null;
    }

    return <div className="component--ProductPreview" style={getRectStyle(layout.position)}>
      {this.getHolderViews(layout)}
    </div>;
  }

  private getHolderViews(layout: ILayout) {
    const holders: IHolder[] = layout.holders;

    if (layout.fixed) {
      return holders.map((holder, index) => {
        return <TDragArea key={holder.id} transitionorder={index} position={holder.position}
          constrain={this.controller.getDragAreaConstrainMargin()}
          onareahover={(event: IEvent, item: DragItem, sourceArea: DragArea, targetArea: DragArea, initial: boolean) => this.controller.onHolderHover(item, sourceArea, targetArea, holder, initial)}
          onareadrop={(event: IEvent, item: DragItem) => this.controller.onHolderContentDrop(item, holder)}
          onarearemove={(event: IEvent, item: DragItem) => this.controller.onHolderContentRemove(item, holder)}
        >
          <DragItem
            data={{ content: holder.content, holderId: holder.id }}
            preventResize={true}
            position={{ left: 0, top: 0, width: holder.position.width, height: holder.position.height }}
            onclick={() => this.controller.onHolderSelect(holder)}
          >
            <Holder holder={holder} className={holder.selected ? "selected" : ""} />
          </DragItem>
        </TDragArea>;
      });
    }

    return <TDragArea
      key={-1}
      grid={setScale({ width: 5, height: 5 }, this.controller.scale)}
      constrain={this.controller.getDragAreaConstrainMargin()}
      onareahover={(event: IEvent, item: DragItem, sourceArea: DragArea, targetArea: DragArea, initial: boolean) => this.controller.onHolderHover(item, sourceArea, targetArea, undefined, initial)}
      onareadrop={(event: IEvent, item: DragItem) => this.controller.onHolderContentDrop(item)}
      onarearemove={(event: IEvent, item: DragItem) => this.controller.onHolderContentRemove(item)}
    >
      {holders.map((holder, index) => {
        return <DragItem
          key={holder.id}
          data={{ content: holder.content, holderId: holder.id }}
          position={holder.position}
          onitemmoveend={(event: IEvent, item: DragItem) => this.controller.onHolderPosition(item, holder)}
          onitemresizeend={(event: IEvent, item: DragItem) => this.controller.onHolderPosition(item, holder)}
          onclick={() => this.controller.onHolderSelect(holder)}
        >
          <Holder holder={holder} className={holder.selected ? "selected" : ""} />
        </DragItem>;
      })}
    </TDragArea>;
  }

  private updateScale(dom: Element) {
    const newScale: number = this.calculateScale(dom);
    if (newScale !== this.controller.scale) {
      this.controller.updateProductPreviewScale(newScale);
    }
  }

  private calculateScale(dom: Element): number {
    if (!dom || !this.controller.actualLayoutRect) {
      return 1;
    }

    const container: Element | null = dom.parentElement;
    if (container === null) {
      return 1;
    }

    return getScale(this.controller.actualLayoutRect as IDimension, getInnerRect(container));
  }
}



