import EasyDeps from "easydeps";
import EventAggregator from "eventaggregator";
import * as m from "mithril";
import ProductManager from "./productManager";
import { IEvent } from "./types";
import { IProduct } from "./types/product";
import { devent } from "./utils/dom";

export interface IProductConfigurator {
  definition: IProduct;
  Main: any;
  targetElement: HTMLElement;
}

const domEA = EventAggregator.getInstance("dom");
const productEA = EventAggregator.getInstance("product");
const deps = EasyDeps.getInstance();

window.addEventListener("mousemove", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("mousemove", event);
}));
window.addEventListener("touchmove", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("touchmove", event);
}));
window.addEventListener("mousedown", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("mousedown", event);
}));
window.addEventListener("touchstart", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("touchstart", event);
}));
window.addEventListener("mouseup", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("mouseup", event);
}));
window.addEventListener("touchend", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("touchend", event);
}));
window.addEventListener("resize", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("resize", event);
}));
window.addEventListener("mousewheel", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("scroll", event);
}));
window.addEventListener("click", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("click", event);
}));
window.addEventListener("keydown", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("keydown", event);
}));
window.addEventListener("keyup", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("keyup", event);
}));
window.addEventListener("contextmenu", ((event: IEvent) => {
  devent(event, {preventDefault: false});
  domEA.emit("contextmenu", event);
}));

productEA.on("change", () => {
  m.redraw();
});

export default (productConfigurator: IProductConfigurator) => {
  const productManager = new ProductManager(productConfigurator.definition);
  deps.register({ domEA, productEA, productManager });
  m.mount(productConfigurator.targetElement, productConfigurator.Main);
  return {
    deps, domEA, productEA,
  };
};



