import EasyDeps from "easydeps";
import ProductManager from "../../base/ProductManager";
import { IHolder, IHolderUpdate, ILayout } from "../../base/types/product";
import logger from "../../base/utils/logger";

export default class LayoutDrawerController {
  public layouts: ILayout[];
  private manager: ProductManager;

  constructor() {
    this.manager = EasyDeps.getInstance().request(["productManager"])[0];
  }

  public refresh() {
    try {
      this.layouts = this.manager.getLayouts();
    } catch (err) {
      logger(err);
      this.layouts = [];
    }
    return {layouts: this.layouts};
  }

  public setCurrentLayout(layoutId: string) {
    this.manager.setCurrentLayout(layoutId);
  }
}



