import inject, { injectClass, injectInstance, injectPrototype } from "./inject";

export interface IInjectTransitionOptions {
  transition?: string;
  group?: string;
  delay?: number;
  pause?: number;
  deep?: boolean | number;
  beforedelay?: boolean | number;
  afterdelay?: number;
}

export interface IInjectTransitionAttrs {
  transition?: string;
  transitiongroup?: string;
  transitiondelay?: number;
  transitionbeforedelay?: boolean | number;
  transitionorder?: number;
}

const getComputedStyleNumber = (dom: Element, property: string): number => {
  const style: string | null = getComputedStyle(dom)[property];
  return style ? parseFloat(style) : 0;
};

const getTransitionDuration = (dom: Element): number => {
  return (
    getComputedStyleNumber(dom, "transitionDelay") +
    getComputedStyleNumber(dom, "transitionDuration")
  ) * 1000;
};

const groups = {};
const getExecutionDelay = (group: string = "main", delay: number = 0): number => {
  const now = Date.now();
  if (groups[group] === undefined) {
    groups[group] = { i: -1, t: now };
  }
  const g = groups[group];
  if (g.t + 50 < now) {
    g.i = 0;
  } else {
    g.i++;
  }
  g.t = now;
  return g.i * delay;
};

const injectAttrs = (v, attrs) => {
  if (typeof attrs !== "object" || attrs === null) {
    throw new Error("Attrs must be an object");
  }
  if (!v.attrs) {
    v.attrs = {};
  }
  v.attrs = { ...v.attrs, ...attrs };
};

const walkNodes = (nodes: any, options: IInjectTransitionOptions, depth: number = -1) => {
  depth++;
  let filtered: any[] = [];
  if (typeof options.deep === "number" && depth > options.deep) {
    return filtered;
  }

  if (typeof nodes !== "object" || nodes === null) {
    return filtered;
  }

  if (Array.isArray(nodes)) {
    nodes.forEach((child) => {
      filtered = [...filtered, ...walkNodes(child, options, depth)];
    });
    return filtered;
  }

  if (nodes.attrs && nodes.attrs.transition) {
    filtered.push(nodes);
  }

  if (Array.isArray(nodes.children)) {
    nodes.children.forEach((child) => {
      filtered = [...filtered, ...walkNodes(child, options, depth)];
    });
  }

  return filtered;
};

const getFullExecutionDelay = (v: any, options: IInjectTransitionOptions) => {
  // const beforedelay: number = (options.beforedelay === true ? getTransitionDuration(v.dom) : (options.beforedelay || 0));
  if (typeof v.attrs.transitionorder === "number" && options.delay) {
    return (v.attrs.transitionorder * options.delay) + (options.pause || 0); // + beforedelay;
  } else {
    return getExecutionDelay(options.group + "before", options.delay) + (options.pause || 0); // + beforedelay;
  }
};

const mergeOptions = (options: IInjectTransitionOptions, attrs: IInjectTransitionAttrs) => {
  const merged = { ...options };
  if (attrs.transition) { merged.transition = attrs.transition; }
  if (attrs.transitiongroup) { merged.group = attrs.transitiongroup; }
  if (attrs.transitiondelay) { merged.delay = attrs.transitiondelay; }
  if (attrs.transitionbeforedelay) { merged.beforedelay = attrs.transitionbeforedelay; }
  return merged;
};

const oncreate = (v: any, options: IInjectTransitionOptions, cb?: Function) => {
  if (!v || !v.dom) {
    return;
  }
  const execDelay = getFullExecutionDelay(v, options);
  // v.dom.style.display = "none";
  setTimeout(() => {
    // v.dom.style.display = "";
    v.dom.classList.remove("before");
    if (typeof cb === "function") {
      cb();
    }
  }, execDelay || 20);
};

const onbeforeremoveChildren = (children: any[]) => {
  if (!Array.isArray(children)) {
    return new Promise((resolve) => resolve());
  }
  return Promise.all(children.map((child) => {
    if (child && child.state && child.state.onbeforeremove) {
      return child.state.onbeforeremove.call(child.state, child);
    }
  }));
};

const onbeforeremove = (v: any, options: IInjectTransitionOptions) => {
  if (!v || !v.dom) {
    return;
  }
  const execDelay = getFullExecutionDelay(v, options);
  const childrenPromise = onbeforeremoveChildren(v.children);
  const promise = new Promise((resolve) => setTimeout(() => {
    v.dom.classList.add("after");
    const duration = getTransitionDuration(v.dom);
    setTimeout(() => resolve(), duration + execDelay);
  }, execDelay));
  return Promise.all([promise, childrenPromise]);
};

const applyClassNames = (node: any, transition: string, initial: boolean) => {
  if (Array.isArray(node)) {
    const className = node[0].attrs ? (node[0].attrs.className || "") : "";
    injectAttrs(node[0], { className: `${className} ${transition} ${initial ? "before" : ""}` });
  } else {
    const className = node.attrs ? (node.attrs.className || "") : "";
    injectAttrs(node, { className: `${className} ${transition} ${initial ? "before" : ""}` });
  }
};

const injectTransition = (component: any, options: IInjectTransitionOptions = {}, injector = injectClass) => {
  if (typeof component !== "function") {
    throw new Error("Component Class is not a valid class (typeof function)");
  }
  if (options.deep === undefined) {
    options.deep = false;
  }
  if (options.beforedelay === undefined) {
    options.beforedelay = 0;
  }
  if (options.afterdelay === undefined) {
    options.afterdelay = 0;
  }
  if (options.delay === undefined) {
    options.delay = 0;
  }
  const lifeCycles = {
    view: (original: Function, v: any) => {
      const node = original(v);
      if (options.deep) {
        walkNodes(node, options).forEach((tNode) => {
          const mergedOptions: IInjectTransitionOptions = mergeOptions(options, tNode.attrs);
          injectAttrs(tNode, {
            className: `${tNode.attrs.className} ${mergedOptions.transition} before`,
            oncreate: (tNodeV) => oncreate(tNodeV, mergedOptions),
            onbeforeremove: (tNodeV) => onbeforeremove(tNodeV, mergedOptions),
          });
        });
      }
      if (options.transition) {
        applyClassNames(node, options.transition, !v.state.created);
      }
      return node;
    },
    oncreate: (original: Function, v: any) => {
      original(v);
      if (options.transition) {
        oncreate(v, options, () => {
          v.state.created = true;
        });
      }
    },
    onbeforeremove: (original: Function, v: any) => {
      return Promise.all([onbeforeremove(v, options), original(v)]);
    },
  };

  return injector(component, lifeCycles);
};

export default injectTransition;
export { getTransitionDuration };



