import * as m from "mithril";
import { exec } from "../../utils/object";
import "./style.scss";

export interface IDialogAttrs {
  className?: string;
  oncontainerclick: (event) => void;
}

export default class Dialog implements m.ClassComponent<IDialogAttrs> {
  public view(v: m.CVnode<IDialogAttrs>) {
    return <div className={`component--Dialog ${v.attrs.className}`}>
      <div className="container" onclick={(event) => exec(v.attrs.oncontainerclick, event)} />
      <div className="dialog">
        {v.children}
      </div>
    </div>;
  }
}



