import EventAggregator, { debounce, ISubscriber } from "eventaggregator";
import * as m from "mithril";
import { IDimension, IEvent, IRect } from "../../types";
import { getRectStyle, setStyle } from "../../utils/dom";
import { setScale } from "../../utils/helpers";
import { domToImage } from "../../utils/image";
import logger from "../../utils/logger";
import { exec } from "../../utils/object";
import Dialog from "../Dialog";
import DragArea from "../Drag/Area";
import DragItem from "../Drag/Item";
import Slider from "../Form/Slider";
import { getAreaScale, getContainerStyle, getImgStyle, getRelativePosition, getRotateStyleProp, getScaledItemRect, initializePreview, rotate, zoom } from "./controller";
import "./style.scss";

export interface IImageEditorAttrs {
  className?: string;
  src: string;
  position: IDimension;
  onclose: (event: IEvent) => void;
  onsubmit: (modified: string, event: IEvent) => void;
}

export default class ImageEditor implements m.ClassComponent<IImageEditorAttrs> {
  private imagePosition: IRect;
  private initialized: boolean = false;
  private containerVnode: m.VnodeDOM<any>;
  private frontImageVnode: m.VnodeDOM<any>;
  private backImageVnode: m.VnodeDOM<any>;
  private imageAreaVnode: m.VnodeDOM<any>;
  private itemVnode: m.VnodeDOM<any, DragItem>;
  private resizeEvent: ISubscriber;
  private scrollEvent: ISubscriber;
  private targetDimension: IDimension;
  private scale = 1;
  private rotateValue = 0;
  private domEA: EventAggregator;
  private zoomLevel = 0;

  public oninit(v: m.CVnode<IImageEditorAttrs>) {
    this.domEA = EventAggregator.getInstance("dom");
    this.targetDimension = { width: v.attrs.position.width, height: v.attrs.position.height };
  }

  public oncreate(v: m.CVnodeDOM<IImageEditorAttrs>) {
    this.resizeEvent = this.domEA.on("resize", (event: IEvent) => {
      this.updateScale();
      this.updateImagePosition();
    });
    this.scrollEvent = this.domEA.on("scroll", (event: IEvent) => {
      if (event.deltaY > 0) {
        this.zoom(-1);
      } else if (event.deltaY < 0) {
        this.zoom(1);
      }
    });
    this.initialize();
  }

  public onbeforeremove(v: m.VnodeDOM<IImageEditorAttrs>) {
    this.resizeEvent.off();
    this.scrollEvent.off();
  }

  public view(v: m.CVnode<IImageEditorAttrs>) {
    const scaledAreaDimension = setScale(this.targetDimension, this.scale);

    return <Dialog className="component--ImageEditor open">
      <div className="area" oninit={(vnode) => this.containerVnode = vnode}>
        <DragArea type="rotater">
          <DragItem type="rotater"
            oninit={(vnode) => this.itemVnode = vnode}
            preventResize={true}
            onitemmove={() => this.updateImagePosition()}
          >
            <img className="back" src={v.attrs.src} oninit={(vnode) => this.backImageVnode = vnode} />
          </DragItem>
        </DragArea>
        <div className="image-area" style={getRectStyle(scaledAreaDimension)} oninit={(vnode) => this.imageAreaVnode = vnode} >
          <img className="front" src={v.attrs.src} oninit={(vnode) => this.frontImageVnode = vnode} />
        </div>
      </div>
      <div className="options">
        <button onclick={(event) => this.zoom(1)}>zoom in</button>
        <button onclick={(event) => this.zoom(-1)}>zoom out</button>
        <Slider label="rotate" min={0} max={360} step={1} value={0} oninput={(value, event) => {
          this.rotateValue = value;
          this.updateImagePosition();
        }} />
      </div>
      <div className="navigation">
        <button onclick={(event) => this.crop(scaledAreaDimension, this.imagePosition, v.attrs.src, v.attrs.onsubmit, event).then(() => v.attrs.onclose(event))}>save</button>
        <button onclick={(event) => exec(v.attrs.onclose, event)}>close</button>
      </div>
    </Dialog>;
  }

  private initialize() {
    if (this.initialized === true) { return; }
    setTimeout(() => {
      try {
        this.updateScale();
        const containerRect = this.containerVnode.dom.getBoundingClientRect();
        const areaRect = this.imageAreaVnode.dom.getBoundingClientRect();
        const itemRect = this.itemVnode.state.getBoundingClientRect();
        this.updateDragItemPosition(getScaledItemRect(containerRect, areaRect, itemRect));
        this.updateImagePosition();
        this.initialized = true;
      } catch (err) {
        logger(err);
        this.initialize();
      }
    }, 25);
  }

  private updateScale() {
    const containerRect = this.containerVnode.dom.getBoundingClientRect();
    this.scale = getAreaScale(this.targetDimension, containerRect);
    m.redraw();
  }

  private updateDragItemPosition(position: IRect) {
    this.itemVnode.state.updatePosition(position);
  }

  private updateImagePosition() {
    const areaRect = this.imageAreaVnode.dom.getBoundingClientRect();
    const itemRect = this.itemVnode.state.getBoundingClientRect();
    this.imagePosition = getRelativePosition(itemRect, areaRect);
    setStyle(this.backImageVnode.dom as HTMLElement, { transform: getRotateStyleProp(this.rotateValue) });
    setStyle(this.frontImageVnode.dom as HTMLElement, { transform: getRotateStyleProp(this.rotateValue), ...getRectStyle(this.imagePosition) });
  }

  private async crop(areaDimension, imagePosition, src, onsubmit, event): Promise<any> {
    // areaDimension = setScale(areaDimension, 1 / this.scale);
    // imagePosition = setScale(imagePosition, 1 / this.scale);
    const previewNode = initializePreview();
    m.render(previewNode,
      <div style={getContainerStyle(areaDimension)}>
        <img src={src} style={getImgStyle(imagePosition, this.rotateValue)} />
      </div>,
    );

    const modifiedSrc = await domToImage(previewNode);
    m.render(previewNode, null);
    exec(onsubmit, modifiedSrc, event);
  }

  private zoom(direction: number) {
    if (direction > 0) {
      this.zoomLevel++;
    } else {
      this.zoomLevel--;
    }
    if (this.zoomLevel >= 15) {
      this.zoomLevel = 15;
      return;
    }
    if (this.zoomLevel <= -15) {
      this.zoomLevel = -15;
      return;
    }
    const containerRect = this.containerVnode.dom.getBoundingClientRect();
    const itemRect = this.itemVnode.state.getBoundingClientRect();
    this.updateDragItemPosition(zoom(containerRect, itemRect, direction));
    this.updateImagePosition();
  }

  private rotate(amount: number) {
    this.rotateValue = rotate(this.rotateValue, amount);
    this.updateImagePosition();
  }
}



