import * as m from "mithril";
import { IMAGE, SHAPE, TEXT } from "../../base/enums/index";
import { IEvent } from "../../base/types/index";
import T from "../../base/utils/mHelpers/injectTransition";
declare var require: any;
// tslint:disable-next-line:no-var-requires
const iconText = require("../../base/assets/icon-text.png");
// tslint:disable-next-line:no-var-requires
const iconImage = require("../../base/assets/icon-image.png");
// tslint:disable-next-line:no-var-requires
const iconShape = require("../../base/assets/icon-shape.png");
// tslint:disable-next-line:no-var-requires
import DragItem from "../../base/components/Drag/Item";
import "./style.scss";

const TDragItem = T(DragItem, { group: "OtherDrawer", delay: 100, transition: "t--slide-down-bounce" });

interface IImage {
  name: string;
  src: string;
}

export interface IOtherDrawerAttrs {
  className?: string;
}

export default class OtherDrawer implements m.ClassComponent<IOtherDrawerAttrs> {
  // private itemsContainer: HTMLElement;

  public view(v: m.CVnode<IOtherDrawerAttrs>) {
    return <div className="component--OtherDrawer">
      <span className="element-description">Text and shapes</span>
      <div className="items">
        {/* <Library className="defaultLib" oncontainercreate={(container) => this.itemsContainer = container}> */}
        <div className="item">
          <TDragItem
            data={{ content: { type: TEXT, value: "TEXT" } }}
            keepPlaceholder={true} preventPosition={true}
            preventResize={true}
          >
            <img src={iconText} alt="text" />
          </TDragItem>
        </div>
        <div className="item">
          <TDragItem
            data={{ content: { type: SHAPE, value: "f101", fontFamily: "Material-Design-Iconic-Font" } }}
            keepPlaceholder={true} preventPosition={true}
            preventResize={true}
          >
            <img src={iconShape} alt="shape" />
          </TDragItem>
          {/* </Library> */}
        </div>
      </div>
    </div>;
  }
}



