import { IMAGE, SHAPE, TEXT } from "./base/enums/index";
import { IHolder, ILayout, IProduct, IProductPart } from "./base/types/product";
import { clone } from "./base/utils/object";

const defaultLayout: ILayout = {
  id: "example",
  position: {
    top: 0,
    left: 0,
    width: 300,
    height: 40,
  },
  holders: [{
    id: 0,
    position: {
      top: 0,
      left: 0,
      width: 300,
      height: 40,
    },
    fixed: false,
    type: ["text",
      "image",
      "shape"],
    content: {
      fontFamily: "sans-serif",
      fontSize: "22",
      align: "center",
      // tslint:disable-next-line:max-line-length
      value: "data:image/jpeg;base64,",
      type: "image",
      // tslint:disable-next-line:max-line-length
      thumb: "data:image/jpeg;base64,",
      // tslint:disable-next-line:max-line-length
      originalValue: "data:image/jpeg;base64,",
      filter: [{
        filter: "blur(20px)",
        transform: "scale(3)",
      }],
    },
    selected: true,
  },
  {
    id: 2,
    position: {
      top: 0,
      left: 70,
      width: 90,
      height: 40,
    },
    fixed: false,
    type: ["text",
      "image",
      "shape"],
    content: {
      fontFamily: "Amatic SC",
      fontSize: "32",
      align: "center",
      type: "text",
      value: "ORANGE",
      color: "#41af68",
      filter: [{
        textShadow: "-1px -1px 1px #fff, 1px 1px 1px #000",
      }],
    },
    selected: false,
  },
  {
    id: 3,
    position: {
      top: 0,
      left: 145,
      width: 70,
      height: 40,
    },
    fixed: false,
    type: ["text",
      "image",
      "shape"],
    content: {
      fontFamily: "Amatic SC",
      fontSize: "32",
      align: "center",
      type: "text",
      value: "JUICE",
      color: "#f08523",
      filter: [{
        textShadow: "-1px -1px 1px #fff, 1px 1px 1px #000",
      }],
    },
    selected: false,
  }],
  fixed: false,
};

const layouts0: ILayout = {
  id: "0",
  position: {
    top: 0,
    left: 0,
    width: 300,
    height: 40,
  },
  holders: [{
    id: 0,
    position: {
      top: 5,
      left: 5,
      width: 90,
      height: 30,
    },
    content: {
      value: "text",
      fontFamily: "sans-serif",
      fontSize: "22",
      align: "center",
      type: TEXT,
    },
    type: [TEXT, SHAPE, IMAGE],
    fixed: true,
  }, {
    id: 1,
    position: {
      top: 5,
      left: 105,
      width: 90,
      height: 30,
    },
    content: {
      value: "text",
      fontFamily: "sans-serif",
      fontSize: "22",
      align: "center",
      type: TEXT,
    },
    type: [TEXT, SHAPE, IMAGE],
    fixed: true,
  }, {
    id: 2,
    position: {
      top: 5,
      left: 205,
      width: 90,
      height: 30,
    },
    content: {
      value: "text",
      fontFamily: "sans-serif",
      fontSize: "22",
      align: "center",
      type: TEXT,
    },
    type: [TEXT, SHAPE, IMAGE],
    fixed: true,
  }],
  fixed: true,
  selected: true,
};

const layouts1: ILayout = {
  id: "1",
  position: {
    top: 0,
    left: 0,
    width: 300,
    height: 40,
  },
  holders: [{
    id: 3,
    position: {
      top: 5,
      left: 5,
      width: 140,
      height: 30,
    },
    content: {
      value: "text",
      type: TEXT,
      fontFamily: "sans-serif",
      fontSize: "22",
      align: "center",
    },
    type: [TEXT],
    fixed: true,
  }, {
    id: 4,
    position: {
      top: 5,
      left: 150,
      width: 145,
      height: 30,
    },
    content: {
      type: IMAGE,
    },
    type: [IMAGE],
    fixed: true,
  }],
  fixed: true,
};

const layouts2: ILayout = {
  id: "2",
  position: {
    top: 0,
    left: 0,
    width: 300,
    height: 40,
  },
  holders: [{
    id: 5,
    position: {
      top: 5,
      left: 5,
      width: 30,
      height: 30,
    },
    content: {
      type: IMAGE,
    },
    type: [IMAGE, SHAPE],
    fixed: true,
  }, {
    id: 6,
    position: {
      top: 5,
      left: 40,
      width: 220,
      height: 30,
    },
    content: {
      type: IMAGE,
    },
    type: [IMAGE, SHAPE],
    fixed: true,
  }, {
    id: 7,
    position: {
      top: 5,
      left: 265,
      width: 30,
      height: 30,
    },
    content: {
      type: IMAGE,
    },
    type: [IMAGE, SHAPE],
    fixed: true,
  }],
  fixed: true,
};

const layouts3: ILayout = {
  id: "3",
  position: {
    top: 0,
    left: 0,
    width: 300,
    height: 40,
  },
  holders: [],
  fixed: false,
};

const layouts: ILayout[] = [layouts0, layouts1, layouts2, layouts3, defaultLayout];

const productParts: IProductPart[] = [{
  id: "default",
  layout: clone(layouts0),
  layouts,
  options: {
    fontFamily: [
      "sans-serif",
      "serif",
      "cursive",
      "fantasy",
      "monospace",
      "Schoolbell",
      "Bungee",
      "Open Sans Condensed",
      "Indie Flower",
      "Lobster",
      "Shadows Into Light",
      "Amatic SC",
    ],
    fontSize: [
      "6",
      "8",
      "10",
      "12",
      "16",
      "18",
      "22",
      "32",
      "42",
      "52",
    ],
    align: [
      "left",
      "center",
      "right",
    ],
    backgroundColor: [
      "transparent",
      "white",
      "black",
      "red",
      "blue",
      "green",
      "orange",
    ],
    color: [
      "black",
      "white",
      "red",
      "blue",
      "green",
      "orange",
    ],
    fontStyle: [
      "normal",
      "italic",
    ],
    textDecoration: [
      "none",
      "overline",
      "line-through",
      "underline",
    ],
    fontWeight: [
      "normal",
      "light",
      "bold",
    ],
    textFilter: [
      { title: "none", style: {} },
      { title: "light blur", style: { filter: "blur(2.5px)" } },
      { title: "medium blur", style: { filter: "blur(5px)" } },
      { title: "high blur", style: { filter: "blur(10px)" } },
      { title: "extreme blur", style: { filter: "blur(20px)" } },
    ],
    imageFilter: [
      { title: "none", style: {} },
      { title: "grayscale", style: { filter: "grayscale(1)" } },
      { title: "darken", style: { filter: "brightness(.5)" } },
      { title: "lighten", style: { filter: "brightness(2)" } },
      { title: "low contrast", style: { filter: "contrast(.5)" } },
      { title: "high contrast", style: { filter: "contrast(2)" } },
      {
        title: "light blur",
        style: {
          filter: "blur(2.5px)", transform: "scale(1.1)",
        },
      },
      {
        title: "medium blur",
        style: {
          filter: "blur(10px)", transform: "scale(1.5)",
        },
      },
      {
        title: "high blur",
        style: {
          filter: "blur(20px)", transform: "scale(3)",
        },
      },
      {
        title: "extreme blur",
        style: {
          filter: "blur(80px)", transform: "scale(12)",
        },
      },
    ],
    shapeFilter: [
      { title: "none", style: {} },
      { title: "light blur", style: { filter: "blur(2.5px)" } },
      { title: "medium blur", style: { filter: "blur(5px)" } },
      { title: "high blur", style: { filter: "blur(10px)" } },
      { title: "extreme blur", style: { filter: "blur(20px)" } },
    ],
  },
}];

const product: IProduct = {
  id: "0",
  productParts,
  defaults: {
    fontFamily: "sans-serif",
    fontSize: "22",
    align: "center",
  },
};

export default product;



