import * as m from "mithril";
import { IEvent } from "../../types";
import { exec } from "../../utils/object";
import "./style.scss";

export type EventCb = (value: string, event: IEvent, setValue: (value: string) => void) => void;

export interface IColorPickerAttrs {
  className?: string;
  default?: string;
  value?: string;
  label?: string;
  style?: object;
  onchange?: EventCb;
  oninput?: EventCb;
}

export default class ColorPicker implements m.ClassComponent<IColorPickerAttrs> {
  public view(v: m.CVnode<IColorPickerAttrs>) {
    const id = Math.random().toString();
    return <div className={"component--ColorPicker" + (v.attrs.className || "")} style={v.attrs.style} >
      <input id={id}
        type="color"
        value={v.attrs.value}
        defaultValue={v.attrs.default}
        onchange={(evt: IEvent) => this.emitEvent(v.attrs.onchange, evt)}
        oninput={(evt: IEvent) => this.emitEvent(v.attrs.oninput, evt)}
      >
      </input>
      {v.attrs.label && <label htmlFor={id}>{v.attrs.label}</label>}
    </div>;
  }

  private emitEvent(eventCb: EventCb | undefined, event: IEvent): void {
    event.redraw = false;
    exec(eventCb, event.target.value, event, (value) => event.target.value = value);
  }
}



