import EventAggregator, { debounce, ISubscriber } from "eventaggregator";
import { IDimension, IEvent, IRect } from "../../types/index";

export default class Controller {
  public container: Element;
  public prev: Element;
  public next: Element;
  private events: ISubscriber[] = [];
  private page: number = 0;
  private interval: number | null = null;
  private domEA: EventAggregator;

  constructor() {
    this.domEA = EventAggregator.getInstance("dom");
  }

  public construct() {
    this.events.push(this.domEA.on("resize", debounce((event: IEvent) => this.refreshAll(), 100)));
  }

  public destruct() {
    this.events.forEach((event) => event.off());
  }

  public refresh(): void {
    setTimeout(() => this.refreshAll());
  }

  public navigate(direction: number) {
    if (this.interval !== null) {
      return;
    }

    this.refreshAll();

    if (direction > 0) {
      this.nextPage();
    } else {
      this.prevPage();
    }
  }

  private isHorizontal(rect: IRect): boolean {
    return rect.width >= rect.height;
  }

  private isContainerHorizontal(): boolean {
    return this.isHorizontal(this.container.getBoundingClientRect());
  }

  private getContainerScrollSize(): number {
    const horizontal: boolean = this.isContainerHorizontal();
    return this.container["scroll" + (horizontal ? "Width" : "Height")];
  }

  private getContainerSize(): number {
    const horizontal: boolean = this.isContainerHorizontal();
    return this.container.getBoundingClientRect()[(horizontal ? "width" : "height")];
  }

  private getContainerScrollStart(): number {
    const horizontal: boolean = this.isContainerHorizontal();
    return this.container["scroll" + (horizontal ? "Left" : "Top")];
  }

  private setContainerScrollStart(start: number): void {
    const horizontal: boolean = this.isContainerHorizontal();
    this.container["scroll" + (horizontal ? "Left" : "Top")] = start;
  }

  private nextPage(): void {
    this.page++;
    const maxPage: number = this.getMaxPage();
    if (this.page > maxPage) {
      this.page = maxPage;
    }
    const containerSize: number = this.getContainerSize();
    const containerScrollSize: number = this.getContainerScrollSize();

    this.interval = setInterval(() => this.slideToNext(containerSize, containerScrollSize), requestAnimationFrame);
  }

  private prevPage(): void {
    this.page--;
    if (this.page < 0) {
      this.page = 0;
    }
    const containerSize: number = this.getContainerSize();

    this.interval = setInterval(() => this.slideToPrev(containerSize), requestAnimationFrame);
  }

  private slideToNext(containerSize: number, containerScrollSize: number): boolean {
    const containerScrollStart: number = this.getContainerScrollStart();
    let dest: number = containerSize * this.page;
    if (dest >= containerScrollSize - containerSize) {
      dest = containerScrollSize - containerSize;
      dest = Math.floor(dest);
    }

    if (containerScrollStart >= dest) {
      this.finishSlide(dest);
      return true;
    }

    this.setContainerScrollStart(containerScrollStart + this.getStepSize(dest, containerScrollStart));
    return false;
  }

  private slideToPrev(containerSize: number): boolean {
    const containerScrollStart: number = this.getContainerScrollStart();
    let dest: number = containerSize * this.page;
    dest = Math.ceil(dest);
    if (dest < 0) {
      dest = 0;
    }

    if (containerScrollStart <= dest) {
      this.finishSlide(dest);
      return true;
    }

    this.setContainerScrollStart(containerScrollStart - this.getStepSize(dest, containerScrollStart));
    return false;
  }

  private finishSlide(dest: number): void {
    this.setContainerScrollStart(dest);
    if (this.interval !== null) {
      clearInterval(this.interval);
      this.interval = null;
    }
    this.setNextState();
    this.setPrevState();
  }

  private setPrevState(): void {
    this.hasPrev() ? this.prev.classList.remove("disabled") : this.prev.classList.add("disabled");
  }

  private setNextState(): void {
    this.hasNext() ? this.next.classList.remove("disabled") : this.next.classList.add("disabled");
  }

  private hasPrev(): boolean {
    return this.page > 0;
  }

  private hasNext(): boolean {
    return this.page + 1 < Math.ceil(parseFloat((this.getContainerScrollSize() / this.getContainerSize()).toFixed(1)));
  }

  private getMaxPage(): number {
    return Math.ceil(this.getContainerScrollSize() / this.getContainerSize());
  }

  private fixPosition(): void {
    const containerSize = this.getContainerSize();
    const containerScrollSize = this.getContainerScrollSize();
    let dest: number = containerSize * this.page;
    if (dest < 0) {
      dest = 0;
    }
    if (dest >= containerScrollSize - containerSize) {
      dest = containerScrollSize - containerSize;
    }
    this.setContainerScrollStart(dest);
  }

  private getStepSize(dest: number, scrollStart: number): number {
    let step: number = 1;
    if (dest > scrollStart) {
      step = (dest - scrollStart) / 10;
    } else {
      step = (scrollStart - dest) / 10;
    }
    return step < 1 ? 1 : step;
  }

  private refreshAll() {
    this.fixPosition();
    this.setPrevState();
    this.setNextState();
  }
}



