import * as Store from "store";
import { pause } from "./helpers";

const cached: any = {};

export const get = (key?: string, sync: boolean = true): any => {
  if (key === undefined) {
    return cached;
  }
  if (!sync) {
    return cached[key];
  }
  if (cached[key] === undefined) {
    cached[key] = Store.get(key);
  }
  return cached[key];
};

export const add = (key: string, data: any, sync: boolean = true): any => {
  get(key, sync);
  if (cached[key] === undefined) {
    set(key, data, sync);
  } else if (Array.isArray(cached[key])) {
    if (Array.isArray(data)) {
      set(key, [...cached[key], ...data], data, sync);
    } else {
      set(key, [...cached[key], data], [data], sync);
    }
  } else if (typeof cached[key] === "object" && cached[key] !== null) {
    if (typeof data === "object" && data !== null) {
      set(key, { ...cached[key], ...data }, data, sync);
    } else {
      throw new Error("Cannot add primitive data on an object, please wrap it in an object first");
    }
  } else {
    throw new Error("Cannot use add on a primitive data type");
  }
  return cached[key];
};

export const set = (key: string, data: any, addition?: any, sync: boolean = true): any => {
  cached[key] = data;
  if (!sync) {
    return cached[key];
  }
  try {
    Store.set(key, data);
  } catch (err) {
    const clear = confirm(
      `LocalStorage is full. Do you want te clear all previously stored images so you can store new ones?
        All previously stored images will still be available in this session.`,
    );
    if (clear === true) {
      Store.remove(key);
      if (addition !== undefined) {
        try {
          Store.set(key, addition);
        } catch (err) {
          alert(
            `The data is to big to add to the LocalStorage. The data will not be persistent.`,
          );
        }
      }
    }
  }
  return cached[key];
};



