import * as m from "mithril";
import { ILayout } from "../base/types/product";
import { getRectStyle } from "../base/utils/dom";
import { cleanString } from "../base/utils/object";

export interface ILayoutAttrs {
  layout: ILayout;
  className?: string;
}

export default class Layout implements m.ClassComponent<ILayoutAttrs> {
  public view(v: m.CVnode<ILayoutAttrs>) {
    return <div
      className={cleanString("layout", v.attrs.className)}
      style={getLayoutStyle(v.attrs.layout)}>
      {v.children}
    </div>;
  }
}

export const getLayoutStyle = (l: ILayout) => {
  const position = getRectStyle(l.position);
  return {
    ...position,
    position: "relative",
    overflow: "hidden",
  };
};



