import EasyDeps from "easydeps";
import EventAggregator, { ISubscriberCallback } from "eventaggregator";
import DragArea from "../../base/components/Drag/Area/index";
import DragItem from "../../base/components/Drag/Item";
import { IMAGE, TEXT } from "../../base/enums";
import ProductManager from "../../base/ProductManager";
import { IOptRect, IOptRectStyle, IRect } from "../../base/types";
import { IContent, IHolder, ILayout, IProductPart } from "../../base/types/product";
import { getRectStyle } from "../../base/utils/dom";
import { getRect, setScale } from "../../base/utils/helpers";
import { compress } from "../../base/utils/image";
import logger from "../../base/utils/logger";

export default class ProductPreviewController {
  public layout?: ILayout;
  public scale: number = 1;
  public actualLayoutRect: IRect;
  private manager: ProductManager;
  private events: any[] = [];
  private domEA: EventAggregator;

  constructor() {
    const [manager, domEA] = EasyDeps.getInstance().request(["productManager", "domEA"]);
    this.manager = manager;
    this.domEA = domEA;
  }

  public refresh() {
    try {
      const productPart = this.manager.getProductPart();
      if (productPart) {
        this.actualLayoutRect = productPart.layout.position;
        this.layout = this.manager.getScaledLayout(productPart.layout);
      } else {
        this.layout = undefined;
      }
    } catch (err) {
      logger(err);
      this.layout = undefined;
    }
    return { layout: this.layout };
  }

  public destruct(): void {
    this.events.forEach((sub) => sub.off());
    this.events = [];
  }

  public addEvent(event: string, callback: ISubscriberCallback): void {
    this.events.push(this.domEA.on(event, callback));
  }

  public onHolderHover(item: DragItem, sourceArea: DragArea, targetArea: DragArea, holder: IHolder | undefined, initial: boolean): boolean {
    const itemRect = item.getBoundingClientRect();
    const areaRect = targetArea.getBoundingClientRect();

    if (targetArea === sourceArea && !targetArea.withinArea(itemRect, this.getDragAreaConstrainMargin())) {
      return false;
    }

    const content: IContent = item.getData("content");
    if (holder === undefined) {
      if (initial && this.layout) {
        const position = item.getPosition();
        const targetHeight = this.layout.position.height;
        item.setStaticPosition({ height: targetHeight, width: position.width * (targetHeight / position.height) });
      }
      return true;
    }

    return (Array.isArray(holder.type) && holder.type.indexOf(content.type) !== -1);
  }

  public onHolderContentDrop(item?: DragItem, holder?: IHolder) {
    let content = { type: TEXT } as IContent;
    let position;

    if (item !== undefined) {
      content = item.getData("content");
    }

    if (holder === undefined) {
      holder = this.manager.addHolder();
      if (item !== undefined) {
        position = item.getPosition({ snapToGrid: true });
      }
    }

    this.manager.updateHolder({ id: holder.id, content, position });
    if (holder.selected !== true) {
      this.manager.setCurrentHolder(holder.id);
    }
  }

  public onHolderContentRemove(item: DragItem, holder?: IHolder): void {
    if (holder === undefined) {
      const holderId = item.getData("holderId");
      if (holderId !== undefined) {
        this.manager.removeHolder(holderId);
      }
    } else {
      this.manager.removeHolderContent(holder.id);
    }
    this.manager.setCurrentHolder();
  }

  public onHolderSelect(holder?: IHolder): void {
    if (holder === undefined) {
      this.manager.setCurrentHolder();
    } else {
      this.manager.setCurrentHolder(holder.id);
    }
  }

  public onHolderPosition(item: DragItem, holder: IHolder) {
    const position = item.getPosition();
    this.manager.updateHolder({ id: holder.id, position });
    if (holder.selected !== true) {
      this.manager.setCurrentHolder(holder.id);
    }
  }

  public getDragAreaConstrainMargin(): any {
    return { margin: 50 * this.scale };
  }

  public updateProductPreviewScale(scale: number) {
    this.scale = scale;
    this.manager.setPreviewScale(scale);
  }

  public getScaledHolder(holder: IHolder): IHolder {
    return this.manager.getScaledHolder(holder);
  }
}



