import { IDimension, IOptDimensionConstrain, IOptRect, IRect } from "../types";

export const rectKeys: string[] = ["left", "top", "width", "height"];
export const rectConstrainKeys: string[] = ["minWidth", "minHeight", "maxWidth", "maxHeight"];

export const getScale = (original: IDimension, target: IDimension, maximize?: boolean): number => {
  const maxWidthScale: number = target.width / original.width;
  const maxHeightScale: number = target.height / original.height;
  if (maximize) {
    return maxHeightScale < maxWidthScale ? maxWidthScale : maxHeightScale;
  } else {
    return maxHeightScale > maxWidthScale ? maxWidthScale : maxHeightScale;
  }
};

export const setScale = (obj: {}, scale: number): {} => {
  return Object.keys(obj).reduce((scaled: {}, key: string) => {
    scaled[key] = obj[key] * scale;
    return scaled;
  }, {});
};

export const round = (obj: {}): {} => {
  return Object.keys(obj).reduce((rounded: {}, key: string) => {
    rounded[key] = Math.round(obj[key]);
    return rounded;
  }, {});
};

export const rectIsEqual = (prev: IOptRect, curr: IOptRect): boolean => { // rename rectIsEqual
  return prev.top === curr.top &&
    prev.left === curr.left &&
    prev.width === curr.width &&
    prev.height === curr.height;
};

export const getRect = (obj: object): IOptRect => {
  const rect: IOptRect = {};
  if (obj["left"] !== undefined) {
    rect.left = obj["left"];
  }
  if (obj["top"] !== undefined) {
    rect.top = obj["top"];
  }
  if (obj["width"] !== undefined) {
    rect.width = obj["width"];
  }
  if (obj["height"] !== undefined) {
    rect.height = obj["height"];
  }
  return rect;
};

export const getConstrainedRect = (rect: IRect, constrain: IOptDimensionConstrain): IRect => {
  if (constrain.minWidth && rect.width < constrain.minWidth) {
    rect.width = constrain.minWidth;
  }

  if (constrain.minHeight && rect.height < constrain.minHeight) {
    rect.height = constrain.minHeight;
  }

  if (constrain.maxWidth && rect.width > constrain.maxWidth) {
    rect.width = constrain.maxWidth;
  }

  if (constrain.maxHeight && rect.height > constrain.maxHeight) {
    rect.height = constrain.maxHeight;
  }

  if (rect.width <= 0) {
    rect.width = constrain.minWidth || 20;
  }
  if (rect.height <= 0) {
    rect.height = constrain.minHeight || 20;
  }

  return rect;
};

export const pause = async (ms: number): Promise<any> => new Promise((resolve) => setTimeout(resolve, ms));



