export function clone<T>(obj: T): T {
  let clonedObj;
  if (typeof obj === "object" && obj !== null) {
    if (Array.isArray(obj)) {
      clonedObj = [];
      obj.forEach((item) => clonedObj.push(clone(item)));
    } else {
      clonedObj = {};
      Object.keys(obj).forEach((key) => clonedObj[key] = clone(obj[key]));
    }
    return clonedObj;
  }
  return obj;
}

export function cleanString(...classNames: any[]): string {
  return classNames.filter((x) => typeof x === "string").map((x) => x.trim()).join(" ");
}

export function isFn(fn?: any): boolean {
  return typeof fn === "function";
}

export function isObj(obj?: any): boolean {
  return obj !== null && typeof obj === "object";
}

export function isPlainObj(obj?: any): boolean {
  return isObj(obj) && !isArr(obj) && !isFn(obj);
}

export function isArr(arr?: any): boolean {
  return Array.isArray(arr);
}

export function exec(fn?: any, ...args: any[]): any {
  if (!isFn(fn)) {
    return;
  }
  return fn.apply(this, args);
}

export function mapRange(value: number, source: number[], target: number[]): number {
  return target[0] + (value - source[0]) * (target[1] - target[0]) / (source[1] - source[0]);
}



