import * as m from "mithril";
import Library from "../../base/components/Library/index";
import { IEvent } from "../../base/types/index";
import { ILayout } from "../../base/types/product";
import T from "../../base/utils/mHelpers/injectTransition";
import Layout from "../Layout";
import Controller from "./controller";
// tslint:disable-next-line:ordered-imports
import "./style.scss";

export interface ILayoutDrawerAttrs {
  className?: string;
}

const TLayout = T(Layout, { group: "LayoutDrawer", delay: 100, transition: "t--slide-down-bounce" });

export default class LayoutDrawer implements m.ClassComponent<ILayoutDrawerAttrs> {
  private controller = new Controller();

  public view(v: m.CVnode<ILayoutDrawerAttrs>) {
    const { layouts } = this.controller.refresh();

    return <div className={`component--LayoutDrawer ${v.attrs.className || ""}`}>
      <span className="element-description">Layouts</span>
      <Library className="defaultLib">
        {layouts.map((layout, index) =>
          <TLayout key={layout.id}
            position={layout.position}
            holders={layout.holders}
            selected={layout.selected}
            onclick={() => this.controller.setCurrentLayout(layout.id)}
          />,
        )}
      </Library>
    </div>;
  }
}



