import EventAggregator, { debounce, ISubscriber } from "eventaggregator";
import * as m from "mithril";
import { IDimension, IEvent } from "../../types";
import { colorPicker, compress, modifyPixels } from "../../utils/image";
import { exec, mapRange } from "../../utils/object";
import Dialog from "../Dialog";
import Slider from "../Form/Slider";
import "./style.scss";

export interface IImageBackgroundRemoverAttrs {
  src: string;
  onclose: (event: IEvent) => void;
  onsubmit: (modified: string, event: IEvent) => void;
  className?: string;
}

export default class ImageBackgroundRemover implements m.ClassComponent<IImageBackgroundRemoverAttrs> {
  private isBusy: boolean;
  private modified = false;
  private pickedColor: { r: number; g: number; b: number; a: number; };
  private margin = 100;
  private src: string;
  private resizeEvent: ISubscriber;
  private domEA: EventAggregator;
  private modifiedSrc: string;
  private colorElement: HTMLElement;
  private canvasElement: HTMLCanvasElement;
  private previewVnode: m.VnodeDOM<any>;
  private compressed = false;
  private changes: any[] = [];

  public oninit(v: m.CVnode<IImageBackgroundRemoverAttrs>) {
    this.src = v.attrs.src;
    this.domEA = EventAggregator.getInstance("dom");
  }

  public onbeforeremove(v: m.CVnodeDOM<IImageBackgroundRemoverAttrs>) {
    this.resizeEvent.off();
  }

  public view(v: m.CVnode<IImageBackgroundRemoverAttrs>) {
    return <Dialog className="component--ImageBackgroundRemover open">
      <div className="color" oncreate={(vnode) => this.colorElement = vnode.dom as HTMLElement} />
      <div className="previews" oninit={(vnode) => this.previewVnode = vnode}>
        <canvas className="original" oncreate={(vnode) => this.buildCanvas(vnode.dom)} onupdate={(vnode) => this.buildCanvas(vnode.dom)} />
        <img className="modified pixelated" src={this.modifiedSrc} />
      </div>
      <div className="options">
        <Slider label="threshold" min={0} max={255} step={1} value={this.margin} oninput={debounce((value, event) => {
          this.margin = parseInt(value, 10);
          this.modified = true;
          this.modifyImage().then((modified) => {
            this.modifiedSrc = modified;
            m.redraw();
          });
        }, 250)} />
      </div>
      <div className="navigation">
        <button onclick={(event) => {
          if (!this.modifiedSrc) {
            exec(v.attrs.onclose, event);
            return;
          }
          if (this.modified) {
            this.modified = false;
            this.changes.push({ color: this.pickedColor, margin: this.margin });
          }
          this.applyModifications(v.attrs.src).then((modified) => {
            exec(v.attrs.onsubmit, modified, event);
            exec(v.attrs.onclose, event);
          });
        }}>save</button>
        <button onclick={(event) => {
          event.redraw = false;
          this.modified = false;
          if (!this.modifiedSrc) {
            return;
          }
          this.src = this.modifiedSrc;
          this.changes.push({ color: this.pickedColor, margin: this.margin });
          this.buildCanvas();
        }}>update</button>
        <button onclick={(event) => {
          exec(v.attrs.onclose, event);
        }}>close</button>
      </div>
    </Dialog>;
  }

  private loadColorPicker(canvas: HTMLCanvasElement) {
    colorPicker(canvas, (r, g, b, a) => {
      this.colorElement.style.backgroundColor = a < 50 ? "white" : `rgb(${r},${g},${b})`;
    }, (r, g, b, a) => {
      this.pickedColor = { r, g, b, a };
      this.modified = true;
      this.modifyImage().then((modified) => {
        this.modifiedSrc = modified;
        m.redraw();
      });
    });
  }

  private async modifyImage(src = this.src, color = this.pickedColor, margin = this.margin): Promise<string> {
    const image = await this.applyModifications(src, [{ color, margin }]);
    return image;
  }

  private async applyModifications(src: string = this.src, modifications = this.changes): Promise<string> {
    if (this.isBusy === true) {
      return src;
    }
    this.isBusy = true;
    const image = await modifyPixels(src, (pixels, row) => {
      for (let i = 0; i < pixels.length; i += 4) {
        // tslint:disable-next-line:prefer-for-of
        for (let mod = 0; mod < modifications.length; mod++) {
          const overlap = Math.abs(pixels[i] - modifications[mod].color.r) + Math.abs(pixels[i + 1] - modifications[mod].color.g) + Math.abs(pixels[i + 2] - modifications[mod].color.b);
          if (overlap < modifications[mod].margin) {
            pixels[i + 3] = 0;
          } else {
            const mapped = mapRange(overlap, [modifications[mod].margin, modifications[mod].margin * 1.5], [0, 255]);
            if (mapped < pixels[i + 3]) {
              pixels[i + 3] = mapped;
            }
          }
        }
      }
      return pixels;
    });
    this.isBusy = false;
    return image;
  }

  private getImageDimension(previewRect, imageWidth, imageHeight) {
    const ratio = imageHeight / imageWidth;
    const width = previewRect.width / 2;
    return { width, height: width * ratio };
  }

  private buildCanvas(canvasElement: HTMLCanvasElement = this.canvasElement, src = this.src) {
    if (this.resizeEvent) {
      this.resizeEvent.off();
    }
    this.canvasElement = canvasElement;
    const previewRect = this.previewVnode.dom.getBoundingClientRect();
    const ctx = canvasElement.getContext("2d");
    if (!ctx) {
      throw new Error("canvas not initialized");
    }
    if (!src) {
      return;
    }
    if (this.compressed === false) {
      compress(src, 1, previewRect.width / 2, previewRect.height, "png", true).then((compressed) => {
        this.compressed = true;
        this.src = compressed;
        this.buildCanvas(canvasElement, src);
      });
      return;
    }
    const image: any = new Image();
    const resize = () => {
      const { width, height } = this.getImageDimension(this.previewVnode.dom.getBoundingClientRect(), image.naturalWidth, image.naturalHeight);
      this.canvasElement.width = width;
      this.canvasElement.height = height;
      ctx.imageSmoothingEnabled = false;
      ctx.drawImage(image, 0, 0, width, height);
    };
    image.onload = () => {
      resize();
      this.resizeEvent = this.domEA.on("resize", () => resize());
      this.loadColorPicker(this.canvasElement);
    };
    image.src = src;
  }
}



