import * as m from "mithril";
import { IEvent } from "../../types/index";
import { getIconClassNames, getIcons } from "../../utils/iconfont";
import { exec } from "../../utils/object";
import "./style.scss";

export type EventCb = (value: string, event: IEvent) => void;

export interface IIconPickerAttrs {
  font: string;
  value?: string;
  className?: string;
  style?: object;
  onchange?: EventCb;
  oninput?: EventCb;
}

const cache = {};

export default class IconPicker implements m.ClassComponent<IIconPickerAttrs> {
  private valueDom: Element;
  private dom: Element;
  private selectedOption = "";
  private clicked: boolean = false;

  public oninit(v: m.CVnode<IIconPickerAttrs>) {
    if (cache[v.attrs.font] === undefined) {
      cache[v.attrs.font] = getIcons(v.attrs.font);
    }
    this.selectedOption = v.attrs.value || cache[v.attrs.font][0];
  }

  public oncreate(v: m.CVnodeDOM<IIconPickerAttrs>) {
    this.dom = v.dom;
  }

  public onbeforeupdate(v: m.CVnode<IIconPickerAttrs>, o: m.CVnodeDOM<IIconPickerAttrs>) {
    return false;
  }

  public view(v: m.CVnode<IIconPickerAttrs>) {
    this.clicked = false;
    return <div className={`component--IconPicker ${v.attrs.className}`} style={{ ...v.attrs.style, fontFamily: v.attrs.font }}
     onclick={(event) => {
      if (this.dom.classList.contains("open")) {
        this.dom.classList.remove("open");
      } else {
        this.dom.classList.add("open");
      }
    }}>
      <div className="value" key={this.selectedOption}><div className={"icon-" + this.selectedOption} oncreate={(vnode) => this.valueDom = vnode.dom} /></div>
      <div className="options">
        {cache[v.attrs.font].map((unicode) =>
          <div className={`option icon-${unicode} ${unicode === this.selectedOption ? "selected" : ""}`}
            onclick={(event) => {
              event.stopPropagation();
              this.dom.classList.remove("open");
              this.selectedOption = unicode;
              this.valueDom.className = "icon-" + unicode;
              this.clicked = true;
              exec(v.attrs.onchange, { font: v.attrs.font, unicode: this.selectedOption }, event);
            }}
            onmouseenter={(event) => {
              event.stopPropagation();
              exec(v.attrs.oninput, { font: v.attrs.font, unicode }, event);
            }}
            onmouseleave={(event) => {
              event.stopPropagation();
              if (this.clicked) {
                event.redraw = false;
                return;
              }
              exec(v.attrs.oninput, { font: v.attrs.font, unicode: this.selectedOption }, event);
            }} />,
        )}
      </div>
    </div>;
  }

  private getOption(item: any) {
    return typeof item === "object" && !Array.isArray(item) && item && item.attrs && item.attrs["data-option"];
  }

  private isSelected(item: any, option?: any): boolean {
    if (option === undefined) {
      option = this.getOption(item);
    }
    return (option && option === this.selectedOption);
  }
}



