import EasyDeps from "easydeps";
import ProductManager from "../../base/ProductManager";
import { IHolder } from "../../base/types/product";
import logger from "../../base/utils/logger";

export default class HolderDrawerUsedController {
  public holders: IHolder[];
  private manager: ProductManager;

  constructor() {
    EasyDeps.getInstance().invoke((productManager) => {
      this.manager = productManager;
    });
  }

  public refresh() {
    try {
      this.holders = this.manager.getHolders();
    } catch (err) {
      this.holders = [];
      logger(err);
    }
    return {holders: this.holders};
  }

  public getScaledHolder(holder: IHolder, scale: number): IHolder {
    return this.manager.getScaledHolder(holder, scale);
  }

  public setCurrentHolder(holder: IHolder) {
    this.manager.setCurrentHolder(holder.id);
  }
}



