import * as m from "mithril";
import { IEvent } from "../../types";
import { exec } from "../../utils/object";
import "./style.scss";

export type EventCb = (value: string, event: IEvent, setValue: (value: string) => void) => void;

export interface IInputAttrs {
  className?: string;
  value?: string;
  multiline?: boolean;
  default?: string;
  placeholder?: string;
  type?: string;
  label?: string;
  autofocus?: boolean;
  style?: object;
  onchange?: EventCb;
  oninput?: EventCb;
  onkeypress?: EventCb;
  onenter?: EventCb;
  onblur?: EventCb;
  onclick?: EventCb;
}

export default class Input implements m.ClassComponent<IInputAttrs> {
  public oncreate(v: m.CVnodeDOM<IInputAttrs>) {
    if (v.attrs.autofocus) {
      setTimeout(() => (v.dom as HTMLInputElement).focus());
    }
  }

  public view(v: m.CVnode<IInputAttrs>) {
    const Tag = (v.attrs.multiline ? "textarea" : "input");
    const id = Math.random().toString();
    return <div className={"component--Input" + (v.attrs.className || "")} style={v.attrs.style} >
      <Tag id={id}
        type={v.attrs.type}
        placeholder={v.attrs.placeholder || ""}
        value={v.attrs.value || ""}
        defaultValue={v.attrs.default || ""}
        onchange={(evt: IEvent) => this.emitEvent(v.attrs.onchange, evt)}
        oninput={(evt: IEvent) => this.emitEvent(v.attrs.oninput, evt)}
        onkeypress={(evt: IEvent) => {
          this.emitEvent(v.attrs.onkeypress, evt);
          if (evt.key === "Enter") {
            this.emitEvent(v.attrs.onenter, evt);
          }
        }}
        onblur={(evt: IEvent) => this.emitEvent(v.attrs.onblur, evt)}
        onclick={(evt: IEvent) => this.emitEvent(v.attrs.onclick, evt)}
      >
      </Tag>
      {v.attrs.label && <label htmlFor={id}>{v.attrs.label}</label>}
    </div>;
  }

  private emitEvent(eventCb: EventCb | undefined, event: IEvent): void {
    event.redraw = false;
    exec(eventCb, event.target.value, event, (value) => event.target.value = value);
  }
}



