export const injectMethod = (object, key, method) => {
  const tmp = object[key];
  object[key] = (v, ...args) => {
    const original = (...oargs) => {
      if (typeof tmp === "function") {
        return tmp.call(v.state, ...oargs);
      }
    };
    return method.call(v.state, original, v, ...args);
  };
};

export const injectPrototype = (component, data) => {
  if (typeof component !== "function") {
    throw new Error("Component must be a function");
  }
  if (component.prototype.injected === true) {
    return;
  }
  component.prototype.injected = true;
  for (const key in data) {
    if (typeof data[key] === "function") {
      injectMethod(component.prototype, key, data[key]);
    }
  }
  return component;
};

export const injectInstance = (instance, data) => {
  if (typeof instance !== "object" || instance === null) {
    throw new Error("Instance must be a object");
  }
  if (instance.injected === true) {
    return;
  }
  instance.injected = true;
  for (const key in data) {
    if (typeof data[key] === "function") {
      injectMethod(instance, key, data[key]);
    }
  }
  return instance;
};

export const injectClass = (parentClass, data) => {
  const newClass = function () {
    for (const key in data) {
      if (typeof data[key] === "function") {
        const tmp = this[key];
        this[key] = (v, ...args) => {
          const original = (...oargs) => {
            if (typeof tmp === "function") {
              return tmp.call(v.state, ...oargs);
            }
          };
          return data[key].call(v.state, original, v, ...args);
        };
      }
    }
  };
  newClass.prototype = new parentClass();
  // newClass.prototype.constructor = newClass;
  return newClass;
};

export default injectInstance;



