
import * as DTI from "dom-to-image";
import API from "./api";
// import * as html2canvas from "html2canvas";
import { getOffsetXY } from "./dom";
import logger from "./logger";
import { IDimension } from "../types";

export const createImage = async (imageData: string, maxWidth?: number, maxHeight?: number): Promise<any> =>
  new Promise((resolve, reject) => {
    const image = new Image();
    image.onload = () => {
      let width = image.naturalWidth;
      let height = image.naturalHeight;
      const ratio = height / width;
      if (ratio < 1) {
        if (maxWidth && width > maxWidth) {
          width = Math.floor(maxWidth);
          height = Math.floor(ratio * maxWidth);
        }
        if (maxHeight && height > maxHeight) {
          height = Math.floor(maxHeight);
          width = Math.floor(ratio * maxHeight);
        }
      } else {
        if (maxHeight && height > maxHeight) {
          height = Math.floor(maxHeight);
          width = Math.floor(ratio * maxHeight);
        }
        if (maxWidth && width > maxWidth) {
          width = Math.floor(maxWidth);
          height = Math.floor(ratio * maxWidth);
        }
      }
      resolve({ image, width, height });
    };
    image.onerror = (err: ErrorEvent) => reject(err);
    image.src = imageData;
  });

export const compress = async (imageData: string, quality: number = 0.9, maxWidth?: number, maxHeight?: number, type: string = "jpeg", pixelated: boolean = false): Promise<string> => {
  if (type === undefined) {
    type = getImageType(imageData) || "jpeg";
  }
  const { image, width, height } = await createImage(imageData, maxWidth, maxHeight);
  if (image.naturalWidth === width && image.naturalHeight === height && quality === 1) {
    return imageData;
  }
  const canvas = document.createElement("canvas");
  canvas.width = width;
  canvas.height = height;
  const ctx = canvas.getContext("2d");
  if (!ctx) {
    throw new Error("canvas not initialized");
  }
  ctx.imageSmoothingEnabled = pixelated;
  ctx.drawImage(image, 0, 0, width, height);
  return canvas.toDataURL("image/" + type, quality);
};

export const getImageType = (base64: string): string => {
  const match = base64.match(/data:image\/([a-zA-Z]*);base64/);
  if (match) {
    return match[1] || "jpeg";
  }
  return "jpeg";
};

export const isBase64 = (base64: string): boolean => {
  const match = base64.match(/data:image\/([a-zA-Z]*);base64/);
  return !!match;
};

export const uploadImage = (image: File, quality?: number, maxWidth?: number, maxHeight?: number): Promise<string> =>
  new Promise((resolve) => {
    const fReader: FileReader = new FileReader();
    fReader.onload = () => compress(fReader.result, quality, maxWidth, maxHeight)
      .then((compressed) => resolve(compressed))
      .catch((err) => resolve(fReader.result));
    fReader.readAsDataURL(image);
  });

export const imageUrlToB64 = (url: string, quality?: number, maxWidth?: number, maxHeight?: number): Promise<string> =>
  new Promise((resolve) => {
    const xhr = new XMLHttpRequest();
    xhr.onload = () => {
      const reader = new FileReader();
      reader.onloadend = () => compress(reader.result, quality, maxWidth, maxHeight)
        .then((compressed) => resolve(compressed))
        .catch((err) => resolve(reader.result));
      reader.readAsDataURL(xhr.response);
    };
    xhr.open("GET", url);
    xhr.responseType = "blob";
    xhr.send();
  });

export const renderServer = async (html: string, dimensions: IDimension) => {
  const res = await fetch("http://localhost:3000/html/", { //
    body: JSON.stringify({ html, dimensions }),
    headers: {
      "Accept": "application/json, text/plain, */*",
      "Content-Type": "application/json",
    },
    method: "POST",
  });
  const blob = await res.blob();
  return await URL.createObjectURL(blob);
};

export const domToImage = async (node: HTMLElement) => {
  return DTI.toPng(node);
};

export const downloadImage = (imageData: string, name: string) => {
  const link = document.createElement("a");
  link.download = name;
  link.href = imageData;
  link.click();
};

export const getThumb = (original: string, quality: number = .5, maxWidth: number = 128, maxHeight: number = 128, type: string = "jpeg", pixelated: boolean = false): Promise<string> =>
  compress(original, quality, maxWidth, maxHeight, type, pixelated);

export const modifyPixels =
  (imageData: string, eachRow: (row: Uint8ClampedArray, index: number) => Uint8ClampedArray | undefined, pixelated: boolean = false): Promise<string> => new Promise((resolve, reject) => {
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");
    if (!ctx) {
      throw new Error("canvas not initialized");
    }
    const image = new Image();
    image.onload = () => {
      canvas.width = image.naturalWidth;
      canvas.height = image.naturalHeight;
      ctx.imageSmoothingEnabled = pixelated;
      ctx.drawImage(image, 0, 0);
      const buffer = ctx.getImageData(0, 0, canvas.width, canvas.height);
      const data = buffer.data;

      const pixelsPerRow = canvas.width * 4;
      for (let h = 0; h < canvas.height; h++) {
        const offset = h * pixelsPerRow;
        const modified = eachRow(data.slice(offset, offset + pixelsPerRow), h);
        if (modified && modified.length === pixelsPerRow) {
          for (let i = 0; i < pixelsPerRow; i++) {
            data[offset + i] = modified[i];
          }
        }
      }

      ctx.putImageData(buffer, 0, 0);
      resolve(canvas.toDataURL("png"));
    };
    image.onerror = (err: ErrorEvent) => reject(err);
    image.src = imageData;
  });

export const colorPicker = (canvas: HTMLCanvasElement, onhover: (...number) => void, onclick: (...number) => void) => {
  const ctx = canvas.getContext("2d");
  if (!ctx) {
    throw new Error("canvas not initialized");
  }

  const pick = (event): number[] => {
    const { x, y } = getOffsetXY(event);
    const pixel = ctx.getImageData(x, y, 1, 1);
    const data = pixel.data;
    return [data[0], data[1], data[2], data[3]];
  };

  canvas.addEventListener("mousemove", (event) => onhover(...pick(event)));
  canvas.addEventListener("mouseup", (event) => onclick(...pick(event)));

  canvas.addEventListener("touchmove", (event) => onhover(...pick(event)));
  canvas.addEventListener("touchend", (event) => onclick(...pick(event)));
};

export const avgPixel = (pixels) => {
  let r = 0;
  let g = 0;
  let b = 0;
  let a = 0;
  for (let i = 0; i < pixels.length; i += 4) {
    if (pixels[i] !== undefined) {
      r += pixels[i];
      g += pixels[i + 1];
      b += pixels[i + 2];
      a += pixels[i + 3];
    }
  }
  const total = pixels.length / 4;
  return [r / total, g / total, b / total, a / total];
};



