import * as m from "mithril";
import { getImageType, getThumb } from "../../base/utils/image";
import Editor from "../Editor";
import Controller from "./controller";
import "./style.scss";

export interface IEditorDrawerAttrs {
  className?: string;
}

export default class EditorDrawer implements m.ClassComponent<IEditorDrawerAttrs> {
  private controller = new Controller();

  public view(v: m.CVnode<IEditorDrawerAttrs>) {
    const { holder, scale } = this.controller.refresh();

    if (holder === undefined) {
      return null;
    }

    return <div className={`component--EditorDrawer ${v.attrs.className || ""}`}>
      <Editor
        key={holder.id}
        holder={holder}
        scale={scale}
        options={this.controller.getEditorOptions()}
        contentoptions={this.controller.options}
        ondepthchange={(change: number) =>
          this.controller.update({ depth: change || 0 })}
        oncontentrotatechange={(change: number) =>
          this.controller.update({ content: { rotate: change || 0 } })}
        oncontentalignchange={(change: number) =>
          this.controller.update({ content: { align: change || "" } })}
        oncontentvaluechange={(change: string) =>
          this.controller.update({ content: { value: change || "" } })}
        oncontentfontsizechange={(change: number) =>
          this.controller.update({ content: { fontSize: change || "" } })}
        oncontentfontfamilychange={(change: string) =>
          this.controller.update({ content: { fontFamily: change || "" } })}
        oncontentfontstylechange={(change: string) =>
          this.controller.update({ content: { fontStyle: change || "" } })}
        oncontentfontweightchange={(change: string) =>
          this.controller.update({ content: { fontWeight: change || "" } })}
        oncontenttextdecorationchange={(change: string) =>
          this.controller.update({ content: { textDecoration: change || "" } })}
        oncontentcolorchange={(change: string) =>
          this.controller.update({ content: { color: change || "" } })}
        oncontentbackgroundcolorchange={(change: string) =>
          this.controller.update({ content: { backgroundColor: change || "" } })}
        oncontentopacitychange={(change: string) =>
          this.controller.update({ content: { opacity: change || "" } })}
        oncontentpixelatedchange={(change: string) =>
          this.controller.update({ content: { pixelated: change || "" } })}
        oncontentfilterchange={(filter: any) => {
          if (!filter) {
            return;
          }
          // if (holder.content && holder.content.filter) {
          // filter = [...holder.content.filter, filter];
          // } else {
          filter = [filter];
          // }
          this.controller.update({ content: { filter } });
        }}
        oncontentimagechange={(value: string) => {
          getThumb(value, undefined, undefined, undefined, getImageType(value))
            .then((thumb) =>
              this.controller.update({ content: { value, thumb } }));
        }}
      />
    </div>;
  }
}



