import * as m from "mithril";
import Checkbox from "../../base/components/Form/Checkbox";
import ColorPicker from "../../base/components/Form/ColorPicker";
import IconPicker from "../../base/components/Form/IconPicker";
import Input from "../../base/components/Form/Input";
import Select from "../../base/components/Form/Select";
import ImageBackgroundRemover from "../../base/components/ImageBackgroundRemover";
import ImageEditor from "../../base/components/ImageEditor";
import { IMAGE, SHAPE, TEXT } from "../../base/enums";
import { IEvent } from "../../base/types";
import { IContentMetaOptions, IHolder } from "../../base/types/product";
import { devent } from "../../base/utils/dom";
import { setScale } from "../../base/utils/helpers";
import { cleanString, exec } from "../../base/utils/object";
import "./style.scss";

export interface IAttrs {
  className?: string;
  holder?: IHolder;
  oncontentchange?: (event: IEvent, content: string) => void;
  ondepthchange?: (event: IEvent, direction: number) => void;
  options: {
    depth: boolean;
    type: string;
  };
  contentoptions?: IContentMetaOptions;
  scale: number;
}

export default class Editor implements m.ClassComponent<IAttrs> {
  private imageBackgroundRemover: boolean = false;
  private imageEditor: boolean = false;
  private imageEditorRotate: boolean = false;

  public view(v: m.CVnode<IAttrs>) {
    if (!v.attrs.holder) {
      return null;
    }
    return <div className={cleanString("editor ", v.attrs.className)}>
      {v.attrs.options.type === TEXT && this.getTextEditorOptions(v.attrs)}
      {v.attrs.options.type === IMAGE && this.getImageEditorOptions(v.attrs)}
      {v.attrs.options.type === SHAPE && this.getShapeEditorOptions(v.attrs)}
      {this.getSharedEditorOptions(v.attrs)}
    </div>;
  }

  private getSharedEditorOptions(attrs) {
    const options: Array<m.Vnode<any>> = [];

    options.push(
      <div className="option">
        <span className="option-description">Background color</span>
        <div className="colorSelectPicker">
          <Select value={attrs.holder.content.backgroundColor || attrs.contentoptions.backgroundColor[0]}
            oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentbackgroundcolorchange, value)}>
            {attrs.contentoptions.backgroundColor.map((backgroundColor) => <span data-option={backgroundColor} style={{ backgroundColor }}>{backgroundColor}</span>)}
          </Select>
          <ColorPicker onchange={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentbackgroundcolorchange, value)} />
        </div>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Transparency</span>
        <Select value={attrs.holder.content.opacity}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentopacitychange, value)}>
          <span data-option={0} style={{ opacity: 1 }}>0%</span>
          <span data-option={.9} style={{ opacity: .9 }}>10%</span>
          <span data-option={.8} style={{ opacity: .8 }}>20%</span>
          <span data-option={.7} style={{ opacity: .7 }}>30%</span>
          <span data-option={.6} style={{ opacity: .6 }}>40%</span>
          <span data-option={.5} style={{ opacity: .5 }}>50%</span>
          <span data-option={.4} style={{ opacity: .4 }}>60%</span>
          <span data-option={.3} style={{ opacity: .3 }}>70%</span>
          <span data-option={.2} style={{ opacity: .2 }}>80%</span>
          <span data-option={.1} style={{ opacity: .1 }}>90%</span>
        </Select>
      </div>,
    );

    if (attrs.options.depth) {
      options.push(
        <div className="option">
          <span className="option-description">Depth</span>
          <button onclick={(event: IEvent): void => devent(event) && exec(attrs.ondepthchange, 1)}>+</button>
          <button onclick={(event: IEvent): void => devent(event) && exec(attrs.ondepthchange, -1)}>-</button>
        </div>,
      );
    }

    return options;
  }

  private getTextEditorOptions(attrs) {
    const options: Array<m.Vnode<any>> = [];

    options.push(
      <div className="option">
        <span className="option-description">Content</span>
        <Input value={attrs.holder.content.value}
          multiline={true}
          autofocus={true}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentvaluechange, value)} />
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Font size</span>
        <Select value={attrs.holder.content.fontSize || attrs.contentoptions.fontSize[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfontsizechange, value)}>
          {attrs.contentoptions.fontSize.map((fontSize) => <span data-option={fontSize}>{fontSize}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Font family</span>
        <Select style={{ width: "200px" }} value={attrs.holder.content.fontFamily || attrs.contentoptions.fontFamily[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfontfamilychange, value)}>
          {attrs.contentoptions.fontFamily.map((fontFamily) => <span data-option={fontFamily} style={{ fontFamily }}>{fontFamily}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Font style</span>
        <Select value={attrs.holder.content.fontStyle || attrs.contentoptions.fontStyle[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfontstylechange, value)}>
          {attrs.contentoptions.fontStyle.map((fontStyle) => <span data-option={fontStyle} style={{ fontStyle }}>{fontStyle}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Font weight</span>
        <Select value={attrs.holder.content.fontWeight || attrs.contentoptions.fontWeight[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfontweightchange, value)}>
          {attrs.contentoptions.fontWeight.map((fontWeight) => <span data-option={fontWeight} style={{ fontWeight }}>{fontWeight}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Font decoration</span>
        <Select value={attrs.holder.content.textDecoration || attrs.contentoptions.textDecoration[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontenttextdecorationchange, value)}>
          {attrs.contentoptions.textDecoration.map((textDecoration) => <span data-option={textDecoration} style={{ textDecoration }}>{textDecoration}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Font color</span>
        <div className="colorSelectPicker">
          <Select value={attrs.holder.content.color || attrs.contentoptions.color[0]}
            oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentcolorchange, value)}>
            {attrs.contentoptions.color.map((color) => <span data-option={color} style={{ color }}>{color}</span>)}
          </Select>
          <ColorPicker onchange={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentcolorchange, value)} />
        </div>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Text align</span>
        <Select value={attrs.holder.content.align || attrs.contentoptions.align[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentalignchange, value)}>
          {attrs.contentoptions.align.map((align) => <span data-option={align} style={{ align }}>{align}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Text filter</span>
        <Select className="filterOptions" value={attrs.holder.content.textFilter || attrs.contentoptions.textFilter[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfilterchange, value)}
        >
          {attrs.contentoptions.textFilter.map((filter) =>
            <span data-option={filter.style}>
              {filter.title}
              <div className="image-wrapper">
                <img src={attrs.holder.content.thumb} style={filter.style} />
              </div>
            </span>,
          )}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Text shadow</span>
        <Select value={ attrs.holder.content.textFilter}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfilterchange, value)}>
          <span data-option={{
            textShadow:
              "2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 5px #000",
          }}>black border</span>
          <span data-option={{ textShadow: "1px 1px #fff, -1px 1px #fff, 1px -1px #fff, -1px -1px #fff, 1px 1px 5px #555" }}>white border</span>
          <span data-option={{ textShadow: "-1px -1px 1px #fff, 1px 1px 1px #000" }}>emboss</span>
          <span data-option={{ textShadow: "1px 2px 2px #fff, 0 0 0 #000, 1px 2px 2px #fff" }}>inset</span>
          <span data-option={{ textShadow: "0 5px 10px black" }}>small black bottom shadow</span>
          <span data-option={{ textShadow: "0 5px 10px white" }}>small white bottom shadow</span>
          <span data-option={{ textShadow: "0 0 20px black" }}>medium black shadow</span>
          <span data-option={{ textShadow: "0 0 20px white" }}>medium white shadow</span>
          <span data-option={{ textShadow: "0 0 30px black" }}>large black shadow</span>
          <span data-option={{ textShadow: "0 0 30px white" }}>large white shadow</span>
        </Select>
      </div>,
    );

    return options;
  }

  private getImageEditorOptions(attrs) {
    const options: Array<m.Vnode<any>> = [];

    if (attrs.holder.content.originalValue) {
      options.push(
        <div className="option">
          <span className="option-description">Reset image</span>
          <button onclick={(event: IEvent) => exec(attrs.oncontentimagechange, attrs.holder.content.originalValue)}>reset</button>
        </div>,
      );
    }

    if (attrs.holder && attrs.holder.content && attrs.holder.content.value && this.imageEditor) {
      options.push(
        <ImageEditor
          position={setScale(attrs.holder.position, attrs.scale)}
          src={attrs.holder.content.value}
          onclose={(event) => this.imageEditor = false}
          onsubmit={(changed: string, original: string) => exec(attrs.oncontentimagechange, changed, original)}
        />,
      );
    }

    options.push(
      <div className="option">
        <span className="option-description">Crop image</span>
        <button onclick={(event: IEvent) => this.imageEditor = true}>Open cropping tool</button>
      </div>,
    );

    if (attrs.holder && attrs.holder.content && attrs.holder.content.value && this.imageBackgroundRemover) {
      options.push(
        <ImageBackgroundRemover
          src={attrs.holder.content.value}
          onclose={(event) => this.imageBackgroundRemover = false}
          onsubmit={(changed: string, original: string) => exec(attrs.oncontentimagechange, changed, original)}
        />,
      );
    }

    options.push(
      <div className="option">
        <span className="option-description">Remove background color</span>
        <button onclick={(event: IEvent) => this.imageBackgroundRemover = true}>Open background remover tool</button>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Image filter</span>
        <Select className="filterOptions" value={attrs.holder.content.imageFilter || attrs.contentoptions.imageFilter[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfilterchange, value)}
        >
          {attrs.contentoptions.imageFilter.map((filter) =>
            <span data-option={filter.style}>
              {filter.title}
              <div className="image-wrapper">
                <img src={attrs.holder.content.thumb} style={filter.style} />
              </div>
            </span>,
          )}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Disable image smoothing</span>
        <Checkbox defaultChecked={attrs.holder.content.pixelated} onclick={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentpixelatedchange, value)} />
      </div>,
    );

    return options;
  }

  private getShapeEditorOptions(attrs) {
    const options: Array<m.Vnode<any>> = [];

    options.push(
      <div className="option">
        <span className="option-description">Icon</span>
        <IconPicker value={attrs.holder.content.value} font={attrs.holder.content.fontFamily}
          oninput={({ unicode, font }, event: IEvent) => {
            devent(event);
            exec(attrs.oncontentvaluechange, unicode);
            exec(attrs.oncontentfontfamilychange, font);
          }}>
        </IconPicker>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Size</span>
        <Select value={attrs.holder.content.fontSize || attrs.contentoptions.fontSize[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfontsizechange, value)}>
          {attrs.contentoptions.fontSize.map((fontSize) => <span data-option={fontSize}>{fontSize}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Color</span>
        <div className="colorSelectPicker">
          <Select value={attrs.holder.content.color || attrs.contentoptions.color[0]}
            oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentcolorchange, value)}>
            {attrs.contentoptions.color.map((color) => <span data-option={color} style={{ color }}>{color}</span>)}
          </Select>
          <ColorPicker onchange={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentcolorchange, value)} />
        </div>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Align</span>
        <Select value={attrs.holder.content.align || attrs.contentoptions.align[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentalignchange, value)}>
          {attrs.contentoptions.align.map((align) => <span data-option={align} style={{ align }}>{align}</span>)}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Filter</span>
        <Select className="filterOptions" value={attrs.holder.content.shapeFilter || attrs.contentoptions.shapeFilter[0]}
          oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfilterchange, value)}
        >
          {attrs.contentoptions.shapeFilter.map((filter) =>
            <span data-option={filter.style}>
              {filter.title}
              <div className="image-wrapper">
                <img src={attrs.holder.content.thumb} style={filter.style} />
              </div>
            </span>,
          )}
        </Select>
      </div>,
    );

    options.push(
      <div className="option">
        <span className="option-description">Shadow</span>
        <Select oninput={(value: string, event: IEvent) => devent(event) && exec(attrs.oncontentfilterchange, value)}>
          <span data-option={{
            textShadow:
              "2px 0 0 #000, -2px 0 0 #000, 0 2px 0 #000, 0 -2px 0 #000, 1px 1px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 5px #000",
          }}>black border</span>
          <span data-option={{ textShadow: "1px 1px #fff, -1px 1px #fff, 1px -1px #fff, -1px -1px #fff, 1px 1px 5px #555" }}>white border</span>
          <span data-option={{ textShadow: "-1px -1px 1px #fff, 1px 1px 1px #000" }}>emboss</span>
          <span data-option={{ textShadow: "1px 2px 2px #fff, 0 0 0 #000, 1px 2px 2px #fff" }}>inset</span>
          <span data-option={{ textShadow: "0 5px 10px black" }}>small black bottom shadow</span>
          <span data-option={{ textShadow: "0 5px 10px white" }}>small white bottom shadow</span>
          <span data-option={{ textShadow: "0 0 20px black" }}>medium black shadow</span>
          <span data-option={{ textShadow: "0 0 20px white" }}>medium white shadow</span>
          <span data-option={{ textShadow: "0 0 30px black" }}>large black shadow</span>
          <span data-option={{ textShadow: "0 0 30px white" }}>large white shadow</span>
        </Select>
      </div>,
    );

    return options;
  }
}



