import Base from "./base";
import Main from "./components/Main";
import definition from "./definition";
declare var process: any;

window["debug"] = process.env.NODE_ENV === "production" ? false : true;

const targetElement = document.getElementById("ojc_root") as HTMLElement;
const { deps, domEA, productEA } = Base({ Main, definition, targetElement });

productEA.on("submit", (event) => {
  // console.log(event);
  window["onproductsubmit"](event);
});



