import * as m from "mithril";
import { devent } from "../../utils/dom";
import { compress, getThumb, imageUrlToB64, isBase64, uploadImage } from "../../utils/image";
import logger from "../../utils/logger";
import { exec } from "../../utils/object";
import Input from "../Form/Input";

export interface IImage {
  name: string;
  src: string;
  thumb?: string;
}

export type Onupload = (event, images: IImage[] | IImage) => void;

export interface IImageUploadAttrs {
  onupload: Onupload;
  className?: string;
  quality?: number;
  max?: number | number[];
  thumb?: number | number[];
  label?: string;
}

export default class ImageUploadUrl implements m.ClassComponent<IImageUploadAttrs> {
  public view(v: m.CVnode<IImageUploadAttrs>) {
    return <div className="component--ImageUploadUrl">
      <Input label={v.attrs.label} placeholder="paste an image url"
        onchange={async (value, event, setValue) => {
          devent(event);
          setValue("");

          const maxwidth = v.attrs.max ? (typeof v.attrs.max === "number" ? v.attrs.max : v.attrs.max[0]) : undefined;
          const maxheight = v.attrs.max ? (typeof v.attrs.max === "number" ? v.attrs.max : v.attrs.max[1]) : undefined;

          const thumbwidth = v.attrs.thumb ? (typeof v.attrs.thumb === "number" ? v.attrs.thumb : v.attrs.thumb[0]) : undefined;
          const thumbheight = v.attrs.thumb ? (typeof v.attrs.thumb === "number" ? v.attrs.thumb : v.attrs.thumb[1]) : undefined;

          let src: string;
          if (isBase64(value)) {
            src = await compress(value, v.attrs.quality, maxwidth, maxheight);
          } else {
            src = await imageUrlToB64(value, v.attrs.quality, maxwidth, maxheight);
          }
          let thumbsrc;
          if (v.attrs.thumb) {
            thumbsrc = await getThumb(src, .5, thumbwidth, thumbheight);
          }
          exec(v.attrs.onupload, event, { src, name: value, thumb: thumbsrc });
        }} />
    </div>;
  }
}



